import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import Toaster from "components/Toaster";
import React from "react";
import { instance } from "../../../actions/axiosInstance";
import "../settings.css";

const initialState = {
  action: "",
  editLine: "",
  editableData: null,
  second: "",
  minute: "",
  hour: "",
  day: "",
  day_of_week: "",
};

const daysList = [
  {
    id: "mon",
    name: "Monday",
  },
  {
    id: "tue",
    name: "Tuesday",
  },
  {
    id: "wed",
    name: "Wednesday",
  },
  {
    id: "thu",
    name: "Thursday",
  },
  {
    id: "fri",
    name: "Friday",
  },
  {
    id: "sat",
    name: "Saturday",
  },
  {
    id: "sun",
    name: "Sunday",
  },
];
export default class CustomTable extends React.Component {
  state = {
    ...initialState,
  };

  resetState = () => this.setState({ ...initialState });

  addNewRow = () => {
    const { type } = this.props;
    const { editableData } = this.state;
    if (this.state.action === "edit") {
      return (
        <ClickAwayListener
          onClickAway={(e) => {
            e.stopPropagation();
            this.submitEntry("edit", editableData, type);
            this.resetState();
          }}
        >
          <tr>
            <td>{editableData.id}</td>
            <td>{editableData.name}</td>
            <td>
              <input
                type="number"
                className={`form-control ${this.state.minute > 59 || this.state.minute < 0 ? "error" : ""}`}
                min={0}
                max={59}
                defaultValue={editableData.minute}
                onInput={(e) =>
                  this.setState({
                    minute: e.target.value,
                  })
                }
              />
            </td>
            <td>
              <input
                type="number"
                className="form-control"
                min={0}
                max={23}
                defaultValue={editableData.hour}
                onInput={(e) =>
                  this.setState({
                    hour: e.target.value,
                  })
                }
              />
            </td>
            <td>
              <input
                type="number"
                min={0}
                max={31}
                className="form-control"
                defaultValue={editableData.day}
                onInput={(e) =>
                  this.setState({
                    day: e.target.value,
                  })
                }
              />
            </td>
            <td>
              <select
                className="form-control"
                defaultValue={editableData.day_of_week}
                // multiple
                // options
                onChange={(e) =>
                  this.setState({
                    day_of_week: e.target.value,
                  })
                }
              >
                <option value="" selected>
                  None
                </option>
                {daysList.map((d) => (
                  <option key={d.id} value={d.id}>
                    {d.name}
                  </option>
                ))}
              </select>
            </td>
          </tr>
        </ClickAwayListener>
      );
    }
  };

  renderRows = (type, data) => {
    const { editableData, editLine } = this.state;
    if (type === "job") {
      return data.map((d, line) => {
        if (!editableData || (editableData && editableData.id !== d.id)) {
          if (line === editLine) {
            this.addNewRow();
          } else {
            return (
              <tr
                onClick={() =>
                  this.setState({
                    action: "edit",
                    editableData: d,
                    editLine: line,
                    minute: d.minute,
                    hour: d.hour,
                    day: d.day,
                    day_of_week: d.day_of_week,
                  })
                }
              >
                <td>{d.id}</td>
                <td>{d.name}</td>
                <td>{d.minute}</td>
                <td>{d.hour}</td>
                <td>{d.day}</td>
                <td>
                  {daysList.find((day) => d.day_of_week == day.id) &&
                    daysList.find((day) => d.day_of_week == day.id).name}
                </td>
              </tr>
            );
          }
        }
        return this.addNewRow();
      });
    }
  };

  submitEntry = (mode, prevData, type) => {
    const URL = `scheduledjob?id=${prevData.id}`;
    const requestType = "post";
    const { minute, hour, day, day_of_week } = this.state;
    if (
      minute == prevData.minute &&
      hour == prevData.hour &&
      day == prevData.day &&
      day_of_week == prevData.day_of_week
    ) {
      this.resetState();
    } else if (!minute && !hour && !day && !day_of_week) {
      this.resetState();
    } else if (minute > 59 || hour > 23 || day > 31) {
      Toaster("Invalid input", "error");
      this.resetState();
    } else if (minute || hour || day || day_of_week) {
      let requestObject = {
        minute: minute,
        hour: hour,
        day: day,
        day_of_week: day_of_week,
      };
      instance[requestType](URL, { ...requestObject }).then((response) => {
        this.props.reloadListing();
        this.resetState();
        const msg = "Updated";
        Toaster(msg + " successfully", "success");
      });
    } else {
      this.resetState();
    }
  };

  render() {
    const { header, data, type } = this.props;
    const { action } = this.state;

    return (
      <div className="clearfix">
        <table className={`custom-table`}>
          <thead>
            <tr>
              {header.map((d, i) => (
                <th style={{ width: (i === 0 || i === 3 || i === 4) && "10%" }}>{d}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {!data.length && !action ? (
              <tr>
                <td colSpan={header.length} className="text-center">
                  No Entries
                </td>
              </tr>
            ) : (
              this.renderRows(type, data)
            )}
          </tbody>
        </table>
      </div>
    );
  }
}
