import { handleAPIError } from "handlers/setters";
import React from "react";
import { Accordion, Card } from "react-bootstrap";
import { instance } from "../../../actions/axiosInstance";
import CustomTable from "./jobtable";

class Backgroundjobs extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      jobs: [],
    };
  }

  componentDidMount() {
    this.getJobs();
  }

  getJobs = () => {
    instance.get(`scheduledjob`)
    .then(response => {
      this.setState({
        jobs: response.data.result,
      })
    }).catch(e => handleAPIError(e));
  }

  render() {
    const {
      jobs,
    } = this.state;

    const jobTableHeader = ["Job Id", "Name", "Minute", "Hour", "Day", "Day of week"];

    return (
      <div className="row">
        <div className="w-100">
          <div className="card-body">
            <div className="row">
              <div className="col-md-12">
                <CustomTable
                  type="job"
                  data={jobs}
                  header={jobTableHeader}
                  reloadListing={this.getJobs}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Backgroundjobs;
