import "assets/scss/table.scss";
import backlog from "containers/backlog";
import PurchaseBacklog from "containers/purchaseBacklog";
import customers from "containers/customers";
import "containers/dashboard/dashboard.css";
import reports from "containers/reports";
import insights from "containers/insights";
import Currency from "containers/settings/payroll/Currency";
import { Entity } from "containers/settings/payroll/Entity";
import FundingDate from "containers/settings/payroll/FundingDate";
import PaySchedule from "containers/settings/payroll/PaySchedule";
import { default as React } from "react";
import { Route, Switch } from "react-router-dom";
import "../assets/css/nav.css";
import "../assets/css/perfect-scrollbar.css";
import Header from "../components/header";
import LeftNav from "../components/leftnav";
import dashboard from "../containers/dashboard";
import payables from "../containers/payables";
import payroll from "../containers/payroll";
import receivables from "../containers/receivables";
import settings from "../containers/settings";
import data from "../containers/settings/data";
import company from "../containers/settings/company/index";
import payablesSettings from "../containers/settings/payables/index";
import profile from "../containers/settings/profile/index";
import receivablesSettings from "../containers/settings/receivables/index";
import vendors from "../containers/vendors";
import fe_config from "constants/Configs";
import TokenManager from "utils/TokenManager";
import get from "lodash/get";

export default function NavWrapper(props) {
  React.useEffect(() => {
    const data = JSON.parse(TokenManager.getUserData());
    if (get(data, "client_id", "") == 10) {
      props.history.push("/signup");
    }
  }, []);

  // <div className={`container-fluid bg-light-grey ${pathname.replace('/', '')}`} id="main-body">
  return (
    <div className={`container-fluid bg-light-grey`} id="main-body">
      <Header {...props} />
      <div className="row otherbody" id="application-wrapper">
        <LeftNav />

        <div className="col pay-rec-wrap mp-0">
          {/* switch body here */}
          <Switch>
            <Route exact path="/" component={dashboard} />
            <Route exact path="/dashboard" component={dashboard} />
            <Route path="/payables" component={payables} />
            <Route path="/payroll" component={payroll} />
            <Route path="/receivables" component={receivables} />
            {<Route exact path="/customers" component={customers} />}
            <Route path="/customers/:id" component={customers} />
            {<Route exact path="/vendors" component={vendors} />}
            <Route path="/vendors/:id" component={vendors} />
            <Route exact path="/settings" component={settings} />
            <Route exact path="/reports" component={reports} />
            <Route exact path="/insights" component={insights} />
            <Route path="/backlog" component={backlog} />
            {fe_config.MODE === "DEV" && <Route path="/purchase-orders" component={PurchaseBacklog} />}
            <Route path="/settings/profile" component={profile} />
            <Route path="/settings/company" component={company} />
            <Route path="/settings/payables" component={payablesSettings} />
            <Route path="/settings/receivables" component={receivablesSettings} />
            <Route path="/settings/payroll/entity" component={Entity} />
            <Route path="/settings/payroll/pay_schedule" component={PaySchedule} />
            <Route path="/settings/payroll/funding_date" component={FundingDate} />
            <Route path="/settings/payroll/currency" component={Currency} />
            <Route path="/settings/data" component={data} />
            <Route path="**" component={() => <div>not found</div>} />
          </Switch>
        </div>
      </div>
    </div>
  );
}
