import React, { useState, useEffect } from "react";
import axios from "axios";
import sortBy from "lodash/sortBy";
import get from "lodash/get";
import { connect } from "react-redux";
import { instance, instanceWithRetry } from "../../../actions/axiosInstance";
import { Accordion, Card } from "react-bootstrap";
import Toaster from "components/Toaster";
import ReactTable from "react-table-v6";
import { Modal, Button } from "react-bootstrap";
import { handleAPIError } from "handlers/setters";
import moment from "moment";
import NumberEditorCell from "components/Table/NumberEditorCell";
import CustomPlaidLink from "components/CustomPlaidLink";
import { setconfirmationboxData, resetconfirmationboxData } from "components/ConfirmationBox";
import TextEditorCell from "components/Table/TextEditorCell";
import { getFAWC } from "utils";
import fe_config from "constants/Configs";

let PREV_URL = "";
function Account(props) {
  let accountmodel = {
    id: 0,
    name: "",
    account_number: "",
    client_id: "",
  };
  const [account, setAccount] = useState(accountmodel);

  const [accountData, setAccountData] = useState([]);
  const [selectedDate, setselectedDate] = useState(null);
  const [isFetching, setIsFetching] = useState(false);
  const [show, setShow] = useState(false);
  const [auto_convert_currencies, setauto_convert_currencies] = useState(false);
  const [base_currency, setbase_currency] = useState("");
  const expand = props.location.hash;

  const handleClose = () => setShow(false);

  useEffect(() => {
    getAccount();
    if (["#bankbalance"].includes(expand)) {
      document.getElementById("accounts").scrollIntoView();
    }
    return () => {
      PREV_URL = "";
    };
  }, []);

  useEffect(() => {
    const URL = `bankbalance?from_date='${selectedDate}'&to_date='${selectedDate}'`;

    if (PREV_URL === URL || !selectedDate || !get(accountData, "[0]", "")) {
      return;
    }
    PREV_URL = URL;

    instance
      .get("client")
      .then((response) => {
        setauto_convert_currencies(get(response, "data.result.auto_convert_currencies", ""));
        setbase_currency(get(response, "data.result.base_currency", ""));
      })
      .catch((e) => handleAPIError(e));
    const temp_accounts_data = accountData.map((x) => ({ ...x, amount: null, recordid: null }));
    setAccountData(temp_accounts_data);
    setIsFetching(true);
    instance.get(URL).then((response) => {
      let {
        data: { result },
      } = response;
      const rows = [];
      temp_accounts_data.forEach((acc) => {
        const thisaccdata = result.find((res) => res.bank_account_id === acc.id);
        if (thisaccdata) {
          rows.push({ ...acc, amount: thisaccdata.amount, recordid: thisaccdata.id });
        } else {
          rows.push({ ...acc, amount: null });
        }
      });

      setAccountData(
        sortBy(
          rows.map((x) => ({ ...x, name: x.name.trim() })),
          ["name"]
        )
      );
      setIsFetching(false);
    });
  }, [accountData, selectedDate, accountData]);

  function getAccount() {
    //todo: get data from api
    let URL = `bankaccount`;
    setAccountData([]);
    setIsFetching(true);
    instance
      .get(URL)
      .then((response) => {
        setAccountData(response.data.result);
        PREV_URL = "";
        setselectedDate(null);
        setselectedDate(selectedDate);
        setIsFetching(false);
        if (localStorage.getItem("selectedBankBalanceCheckDate")) {
          setselectedDate(localStorage.getItem("selectedBankBalanceCheckDate"));
          document.getElementById("accounts").scrollIntoView();
          localStorage.removeItem("selectedBankBalanceCheck");
        } else {
          setselectedDate(moment().format("MM/DD/YYYY"));
        }
      })
      .catch((e) => handleAPIError(e));
    instance
      .get("client")
      .then((response) => {
        setauto_convert_currencies(get(response, "data.result[0].auto_convert_currencies", ""));
      })
      .catch((e) => handleAPIError(e));
  }

  function updatePlaidToken(bankDetails) {
    if (bankDetails.status === 1) {
      instance
        .get(`bankaccount/renewtoken?id=${bankDetails.id}`)
        .then((response) => {
          const linkHandler = window.Plaid.create({
            env: fe_config.PLAID_env,
            clientName: "Payference",
            key: fe_config.PLAID_publicKey,
            product: ["transactions"],
            token: response.data.public_token,
            onSuccess: function (public_token, metadata) {
              //on success
              getAccount();
            },
            onExit: function (err, metadata) {
              //on exit
            },
          });
          linkHandler.open();
        })
        .catch((e) => handleAPIError(e));
    }
  }

  function addAccount() {
    let acc = {
      name: account.name,
      account_number: account.account_number,
      ismanual: true,
    };
    instance.post("bankaccount", { ...acc }).then(() => {
      getAccount();
      clearAccount();
      handleClose();
      Toaster("Account added successfully", "success");
    });
  }

  const setconversion = () => {
    // return;
    instance
      .post("client", {
        auto_convert_currencies: !auto_convert_currencies,
      })
      .then(() => {
        setauto_convert_currencies(!auto_convert_currencies);
      });
  };

  const clearAccount = () => {
    account.bankname = "";
    account.accountno = "";
    account.routingno = "";
  };

  const onChange = (e) => {
    const { name, value } = e.target;
    setAccount((prevState) => ({ ...prevState, [name]: value }));
  };

  const onBlurr = (e) => {
    const { name, value } = e.target;
    setAccount((prevState) => ({ ...prevState, [name]: value }));
  };

  const columns = [
    {
      Header: "Bank name",
      accessor: "name",
      className: "mr-3",
      width: 180,
      Cell: (props) => {
        return (
          <div onClick={(e) => e.stopPropagation()}>
            <TextEditorCell
              {...props}
              iscellcomponent
              updateValue={(value) => {
                const { original } = props;
                const postdata = {
                  name: value || "",
                };
                const URL = `bankaccount?id=${original.id}`;
                instance.put(URL, postdata).then(() => {
                  localStorage.setItem("refresh_reports", true);
                  Toaster("Updated successfully", "success");
                });
              }}
            />
          </div>
        );
      },
    },
    {
      Header: "Account Number",
      accessor: "account_number",
      Cell: (props) => {
        return `XXXX-XXXX-XXXX-${props.original.account_number}`;
      },
    },
    {
      Header: () => (
        <div className="mr-2">
          <span style={{ position: "absolute", left: 0 }}>
            <i
              className={`fa fa-angle-left ${
                moment().subtract(10, "days").format("MM/DD/YYYY") === selectedDate && "text-transparent"
              }`}
              onClick={() => {
                if (moment().subtract(10, "days").format("MM/DD/YYYY") === selectedDate) {
                  return;
                }
                setselectedDate(moment(selectedDate, "MM/DD/YYYY").subtract(1, "day").format("MM/DD/YYYY"));
              }}
              id="prevDate"
              aria-hidden="true"
            />
          </span>
          <span style={{ position: "absolute", top: 5, left: 30 }}>
            {moment(selectedDate, "MM/DD/YYYY").format("ddd, DD MMM, YYYY")}
          </span>
          <span style={{ position: "absolute", right: 10 }}>
            <i
              className={`fa fa-angle-right ${moment().format("MM/DD/YYYY") === selectedDate && "text-transparent"}`}
              id="nextDate"
              aria-hidden="true"
              onClick={() => {
                if (moment().format("MM/DD/YYYY") === selectedDate) {
                  return;
                }
                setselectedDate(moment(selectedDate, "MM/DD/YYYY").add(1, "day").format("MM/DD/YYYY"));
              }}
            />
          </span>
        </div>
      ),
      accessor: "amount",
      className: "text-right mr-3",
      width: 180,
      Footer: (props) => {
        return (
          <div>
            <span className="pull-left font-weight-bold">Total:</span>
            <span className="pull-right mr-2">{getFAWC(accountData.reduce((a, b) => a + Number(b.amount), 0))}</span>
          </div>
        );
      },
      Cell: (props) => {
        return (
          <div onClick={(e) => e.stopPropagation()}>
            <NumberEditorCell
              {...props}
              iscellcomponent
              updateValue={(value) => {
                const { original, index } = props;
                const updatebalance = (v) => {
                  const postdata = {
                    records: [{ date: selectedDate, amount: v || "0", bank_account_id: original.id }],
                  };
                  const URL = original.recordid ? `bankbalance?id=${original.recordid}` : `bankbalance`;
                  instance.put(URL, postdata).then(() => {
                    localStorage.setItem("refresh_reports", true);
                    let updatedAccData = accountData;
                    updatedAccData[index].amount = v;
                    setAccountData([]);
                    setAccountData(updatedAccData);
                  });
                };
                if (auto_convert_currencies && base_currency && original.currency) {
                  axios
                    .get(`https://api.exchangeratesapi.io/latest?base=${original.currency}&symbols=${base_currency}`)
                    .then((resp) => {
                      const multiplier = get(resp, `data.rates[${base_currency}]`, 1);
                      const convertedvalue = Number(Number(value || 0) * Number(multiplier)).toFixed(2);
                      updatebalance(convertedvalue);
                      Toaster(`Converted successfully: ${original.currency} ${value} to ${base_currency} ${convertedvalue}`, "success", 4000);
                    })
                    .catch(() => Toaster("Automatic currency conversion failed. Please convert manually"));
                } else {
                  updatebalance(value);
                }
              }}
            />
          </div>
        );
      },
    },
    {
      Header: "",
      accessor: "id",
      width: 100,
      Cell: (props) => (
        <span className="">
          {get(props, "original.status", "") ? (
            <i className="fa fa-key t-red cursor-pointer mr-2" title="Authentication Needed" />
          ) : null}
          <i
            className={
              get(props, "original.status", "")
                ? "fa fa-trash-o ml-4 cursor-pointer"
                : "fa fa-trash-o ml-5 cursor-pointer"
            }
            aria-hidden="true"
            title="Delete Account"
            onClick={(e) => {
              e.stopPropagation();
              const { original, index } = props;
              setconfirmationboxData({
                variant: "warning",
                msg: "Are you sure you want to delete?",
                onSave: () => {
                  resetconfirmationboxData();

                  instance.put(`bankaccount/delete?id=${original.id}`, {}).then(() => {
                    localStorage.setItem("refresh_reports", true);
                    Toaster("Deleted successfully", "success");
                    let updatedaccountData = accountData.filter((x, i) => i !== index);
                    setAccountData([]);
                    setAccountData(updatedaccountData);
                  });
                },
              });
            }}
          />
        </span>
      ),
    },
  ];

  return (
    <Accordion defaultActiveKey={expand === "#bankbalance" && "0"} className="col-md-12">
      <Accordion.Toggle className="border mt-4 px-3 cursour-pointer bg-light-grey" as={Card.Header} eventKey="0">
        <h6 className="my-0">Accounts</h6>
      </Accordion.Toggle>
      <Accordion.Collapse className="py-2" eventKey="0">
        <div>
          <div className="row">
            <div className="col-md-8">
              <button
                type="button"
                onClick={() => setShow(true)}
                className="btn subbtn btn-primary pull-left m-2 p-2 mr-3"
                data-toggle="modal"
                data-target="#myModal"
              >
                Add account
              </button>
              <label className="ml-3 pull-right mt-3 pt-1">
                <span style={{ 
                  position: "absolute",
                  right: "52px"
                 }}>Auto convert currencies</span>
                <span>
                  <input
                    style={{
                      width: 20,
                      height: 20,
                      margin: 0,
                      padding: 0,
                      position: "absolute",
                      right: "0px",
                      top: 22
                    }}
                    type="checkbox"
                    className="mr-3 cursour-pointer"
                    placeholder="Enter ..."
                    onClick={setconversion}
                    checked={auto_convert_currencies}
                  />
                </span>
              </label>
              <CustomPlaidLink getAccount={getAccount} />
              <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                  <Modal.Title>
                    <h3>Add Account</h3>
                  </Modal.Title>
                </Modal.Header>
                <Modal.Body closeButton>
                  <div className="form-group">
                    <span>
                      <label>Bank name</label>
                    </span>
                    <input
                      type="text"
                      name="name"
                      value={account.name}
                      onBlur={onBlurr}
                      onFocus={onChange}
                      onChange={onChange}
                      className="form-control"
                      placeholder="Enter ..."
                    />
                  </div>
                  <div className="form-group">
                    <span>
                      <label>Account no</label>
                    </span>
                    <input
                      type="text"
                      name="account_number"
                      value={account.account_number}
                      onBlur={onBlurr}
                      onFocus={onChange}
                      onChange={onChange}
                      className="form-control"
                      placeholder="Enter ..."
                    />
                  </div>
                  {/* 
                  // No such field in DB
                  <div className="form-group">
                    <span>
                      <label>Routing number</label>
                    </span>
                    <input
                      type="text"
                      name="client_id"
                      value={account.client_id}
                      onBlur={onBlurr}
                      onFocus={onChange}
                      onChange={onChange}
                      className="form-control"
                      placeholder="Enter ..."
                    />
                  </div> */}
                </Modal.Body>
                <Modal.Footer>
                  <Button
                    variant="primary"
                    disabled={!(account && account.name && account.account_number)}
                    onClick={addAccount}
                  >
                    Add
                  </Button>
                </Modal.Footer>
              </Modal>
            </div>
          </div>
          <div className="row">
            <div className="col-md-8">
              <div className="paynewdetsectran tablemargin">
                <ReactTable
                  noDataText={isFetching ? "" : "No data found"}
                  pageSize={accountData.length > 1 ? accountData.length : 1}
                  // pageSize={30}
                  loading={isFetching}
                  sortable={false}
                  resizable={true}
                  showPagination={false}
                  columns={columns}
                  data={accountData}
                  getTrGroupProps={(state, rowInfo, column) => {
                    return {
                      onClick: (e) => updatePlaidToken(rowInfo.original),
                      style: {
                        border: rowInfo && rowInfo.original.status === 1 ? "1px solid red" : "",
                      },
                    };
                  }}
                />
              </div>
            </div>
            {/* <div className="col-md-1">
              <i
                style={{ marginTop: 28, marginLeft: -22, fontSize: 20 }}
                onClick={e => {}}
                className={`fa fa-plus cashinflowclick`}
                title="Click to add item"
                aria-hidden="true"
              />
            </div> */}
          </div>
        </div>
      </Accordion.Collapse>
    </Accordion>
  );
}

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(Account);
