import { ReducersTypes } from "../../constants/ReducersTypes";

export const receivables = (
  state = {
    data: "receivables data"
  },
  action
) => {
  switch (action.type) {
    case ReducersTypes.RECEIVABLES:
      return { data: action.payload.data };
    default:
      return state;
  }
};

export default { receivables };
