import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import Toaster from "components/Toaster";
import { Accordion, Card } from "react-bootstrap";
import { instance } from "../../../actions/axiosInstance";
import TokenManager from "utils/TokenManager";
import Configs from "constants/Configs";
import axios from "axios";
import { handleAPIError } from "handlers/setters";
import { passwordRegEx } from "constants/regex";

function Password(props) {
  let password = {
    currentPassword: "",
    newPassword: "",
    confirmPassword: ""
  };

  const [userPassword, setUserPassword] = useState(password);
  const [isProcessing, setIsProcessing] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);

  const expand = props.location.hash;
  useEffect(() => {
    if (['#password'].includes(expand)) {
      document.getElementById("password").scrollIntoView()
    }
  }, []);

  useEffect(() => {}, [userPassword]);

  const update = e => {
    e.preventDefault();

    let URL = "user/setpassword";
    let userName = TokenManager.getUserName();
    if (!passwordRegEx.test(userPassword.newPassword)){
      Toaster("Password must contain in 8 characters and 1 digit!", "error");
    } else if(userPassword.newPassword !== userPassword.confirmPassword) {
      Toaster("New password and Confirm password did not match!", "error");
    } else if(userPassword.newPassword === userPassword.currentPassword) {
      Toaster("New password should be different than existing password!", "error");
    } else {
      setIsProcessing(true);
      axios
        .get(`${Configs.API_URL}token`, {
          auth: {
            username: userName,
            password: userPassword.currentPassword
          }
        })
        .then(response => {
          if (response != null && response.data != null && response.data.token != null) {
            instance
              .post(
                URL,
                { password: userPassword.confirmPassword }
              )
              .then(response => {
                Toaster("Password updated successfully", "success");
                setUserPassword(password);
              })
              .catch(e => handleAPIError(e));
          }
          setIsProcessing(false);
        })
        .catch(error => {
          Toaster("Please enter correct current password!", "error");
          setIsProcessing(false);
        });
    }
  };

  const onChange = e => {
    const { name, value } = e.target;
    setUserPassword(prevState => ({ ...prevState, [name]: value }));
  };

  const onBlurr = e => {
    const { name, value } = e.target;
    setUserPassword(prevState => ({ ...prevState, [name]: value }));
  };

  return (
    <Accordion className="col-md-12" defaultActiveKey={expand === '#password' && "0"} id="password">
      <Accordion.Toggle className="border mt-4 px-3 cursour-pointer bg-light-grey" as={Card.Header} eventKey="0">
        <h6 className="my-0">Change Password</h6>
      </Accordion.Toggle>
      <Accordion.Collapse className="p-2 px-3" eventKey="0">
        <form onSubmit={update} action method="post">
          <div className="row">
            <div className="col-md-3">
              <div className="form-group">
                <span>
                  <label>Current password</label>
                </span>
                <input
                  type="password"
                  name="currentPassword"
                  className="form-control"
                  placeholder="Enter ..."
                  autoComplete="off"
                  onFocus={onChange}
                  onChange={onChange}
                  onBlur={onBlurr}
                  required
                  value={userPassword.currentPassword}
                />
              </div>
            </div>
            <div className="col-md-3">
              <div className="form-group">
                <span>
                  <label>New password</label>
                </span>
                <input
                  type="password"
                  name="newPassword"
                  className="form-control"
                  placeholder="8 char, min 1 digit"
                  autoComplete="off"
                  onFocus={onChange}
                  onChange={onChange}
                  onBlur={onBlurr}
                  required
                  value={userPassword.newPassword}
                />
              </div>
            </div>
            <div className="col-md-3">
              <div className="form-group">
                <span>
                  <label>Confirm password</label>
                </span>
                <div className="confirm-password">
                  <input
                    type={showConfirmPassword ? 'text' : 'password'}
                    name="confirmPassword"
                    className="form-control"
                    placeholder="Enter ..."
                    autoComplete="off"
                    onFocus={onChange}
                    onChange={onChange}
                    onBlur={onBlurr}
                    required
                    value={userPassword.confirmPassword}
                  />
                  <span
                    className="show-hide-toggle eye-password"
                    role="presentation"
                    onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                  >
                    {showConfirmPassword ? (
                      <i className="fa fa-eye" style={{ cursor: "pointer", fontSize: "15px" }} title="Show Password" />
                    ) : (
                      <i className="fa fa-eye-slash" style={{ cursor: "pointer", fontSize: "15px" }} title="Show Password" />
                    )}
                  </span>
                </div>
              </div>
            </div>
            <div className="col-md-3">
              <div className="form-group text-right" style={{ marginTop: "30px" }}>
                <button type="submit" className="btn subbtn btn-success">
                  {isProcessing ? (
                    <span>
                      Updating <i class="fa fa-spinner fa-spin" style={{ fontSize: "11px" }} aria-hidden="true"></i>
                    </span>
                  ) : (
                    <span>Update</span>
                  )}
                </button>
              </div>
            </div>
          </div>
        </form>
      </Accordion.Collapse>
    </Accordion>
  );
}

const mapStateToProps = state => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(Password);
