import { instance } from "actions/axiosInstance";
import axios from "axios";
import Configs from "constants/Configs";
import get from "lodash/get";
import React, { Component } from "react";
import { Carousel } from "react-bootstrap";
import { connect } from "react-redux";
import TokenManager from "utils/TokenManager";
import Advanced from "./advanced.js";
import Basic from "./basic.js";
import LinkAccounts from "./linkaccounts.js";
import "./style.scss";
const activeIndexClasses = {
  0: { classes: "", styles: {} },
  1: { classes: "", styles: {} },
  2: { classes: "", styles: {} }
};

class Login extends Component {
  state = {
    showPassword: false,
    email: {
      value: "",
      isFocused: false,
      error: ""
    },
    password: {
      value: "",
      isFocused: false,
      error: ""
    },
    formError: "",
    activeIndex: 0
  };

  componentDidMount() {
    const data = JSON.parse(TokenManager.getUserData());
    if(get(data, "client_id", "") == 10) {
      this.setState({ activeIndex: 2 })
    }
  }

  onSubmit = e => {
    e.preventDefault();
    return;
    const { email, password } = e.target.elements;

    this.setState(
      {
        email: {
          value: email.value,
          isFocused: false,
          error: !email.value ? "required" : ""
        },
        password: {
          value: password.value,
          isFocused: false,
          error: !password.value ? "required" : ""
        }
      },
      () => {
        const { email, password } = this.state;
        if (email.value && password.value) {
          axios
            .get(`${Configs.API_URL}token`, {
              auth: {
                username: this.state.email.value,
                password: this.state.password.value
              }
            })
            .then(response => {
              TokenManager.set(response.data.token, response.data["expires at"]);
              TokenManager.setUserName(this.state.email.value, response.data["id"]);

              let URL = `/client/user?id=${TokenManager.getUserId()}`;
              instance
                .get(URL, {
                  auth: {
                    username: TokenManager.get(),
                    password: Math.random().toString(20)
                  }
                })
                .then(resp => {
                  const datatemp = {
                    ...response.data,
                    ...get(resp, "data.result[0]", {})
                  };
                  TokenManager.setUserData(datatemp);

                  if (!response.data.status) {
                    this.props.history.push("/reset-password");
                  } else {
                    window.location.reload();
                  }
                });
            })
            .catch(error => {
              this.setState({
                formError: "Invalid Credentials please try again."
              });
            });
        }
      }
    );
  };

  onChange = e => {
    const { name, value } = e.target;
    this.setState({
      [name]: {
        value,
        isFocused: true,
        error: !value ? "required" : ""
      }
    });
  };

  onBlurr = e => {
    const { name, value } = e.target;
    this.setState({
      [name]: {
        value,
        isFocused: false,
        error: this.state[name].error
      }
    });
  };

  togglePassword = () => {
    this.setState({
      showPassword: !this.state.showPassword
    });
  };

  changeActiveIndex = index => {
    this.setState({ activeIndex: index });
  };

  render() {
    const { showPassword, email, password, formError, activeIndex } = this.state;

    return (
      <div style={activeIndexClasses[activeIndex].styles} className={`signuppage`}>
      
          <div>
            <Carousel className="w-100" slide={false} activeIndex={activeIndex} indicators={false}>
              <Carousel.Item>
                <Basic changeActiveIndex={this.changeActiveIndex} />
              </Carousel.Item>

              <Carousel.Item className="">
                <Advanced changeActiveIndex={this.changeActiveIndex} />
              </Carousel.Item>

              <Carousel.Item>
                <LinkAccounts changeActiveIndex={this.changeActiveIndex} />
              </Carousel.Item>
            </Carousel>
          </div>
        </div>
    );
  }
}

const mapStateToProps = state => ({ login: state });

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(Login);
