import { resetconfirmationboxData, setconfirmationboxData } from "components/ConfirmationBox";
import Toaster from "components/Toaster";
import { handleAPIError } from "handlers/setters";
import React, { useEffect, useState } from "react";
import { Accordion, Card, Modal } from "react-bootstrap";
import { connect } from "react-redux";
import ReactTable from "react-table-v6";
import TokenManager from "utils/TokenManager";
import { instance } from "../../../actions/axiosInstance";
import TextEditorCell from "components/Table/TextEditorCell";
import SelectEditorCell from "components/Table/SelectEditorCell";
import get from "lodash/get";
import { emailRegEx, passwordRegEx } from "constants/regex";

function User(props) {
  let usermodel = {
    id: 0,
    name: "",
    department: "",
    role: "",
    email: "",
  };
  const [user, setUser] = useState(usermodel);
  const [addInProgress, setaddInProgress] = useState(false);
  const [isexpanded, setisexpanded] = useState(false);
  const [userData, setUserData] = useState([]);
  const [isFetching, setIsFetching] = useState(false);
  const [selectedUser, setselectedUser] = useState(null);
  const isAdmin = TokenManager.getIsAdmin();
  const expand = props.location.hash;
  const [userPassword, setUserPassword] = useState({
    newPassword: "",
    confirmPassword: "",
  });
  const [isProcessing, setIsProcessing] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [showError, setshowError] = useState(false);
  

  function getUser() {
    //todo: get data from api
    let URL = `client/user`;
    setIsFetching(true);
    instance
      .get(URL)
      .then((response) => {
        setIsFetching(false);
        setUserData(response.data.result);
      })
      .catch((e) => {
        setIsFetching(false);
      });
  }

  const update = (e) => {
    e.preventDefault();
    let URL = "user/setpassword";
    if (!passwordRegEx.test(userPassword.newPassword)) {
      setshowError(true);
      Toaster("Password must contain in 8 characters and 1 digit!", "error");
    } else if (userPassword.newPassword !== userPassword.confirmPassword) {
      setshowError(true);
      Toaster("New password and Confirm password did not match!", "error");
    } else {
      instance
        .post(URL, { password: userPassword.confirmPassword, userid: selectedUser.id })
        .then((response) => {
          Toaster("Password updated successfully", "success");
          setUserPassword({
            newPassword: "",
            confirmPassword: "",
          });
          setshowError(false);
          setselectedUser(null);
        })
        .catch((e) => handleAPIError(e));
    }
  };

  useEffect(() => {
    getUser();
    if (["#users"].includes(expand)) {
      document.getElementById("users").scrollIntoView();
    }
  }, []);

  function addUser() {
    if (!user.email || !user.role) {
      return;
    }

    const postdata = { name: user.name, email: user.email, roles: [user.role], department: user.department };

    instance
      .post(`client/inviteuser`, postdata)
      .then((resp) => {
        Toaster("Invitation sent successfully", "success");
        clearAccount();
        setUser(usermodel);
        getUser();
        setaddInProgress(false);
      })
      .catch((e) => {
        handleAPIError(e);
        Toaster("User already exists", "error");
      });
  }

  const clearAccount = () => {
    user.name = "";
    user.department = "";
    user.role = "";
    user.email = "";
  };

  const onChangeNewUser = (e) => {
    const { name, value } = e.target;
    setUser({ ...user, [name]: value });
  };

  const onBlurrNewUser = (e) => {
    const { name, value } = e.target;
    setUser({ ...user, [name]: value });
  };

  const onChange = (e) => {
    const { name, value } = e.target;
    setUserPassword({ ...userPassword, [name]: value });
  };

  const onBlurr = (e) => {
    const { name, value } = e.target;
    setUserPassword({ ...userPassword, [name]: value });
  };

  const columns = [
    {
      Header: "ID",
      accessor: "id",
      className: "ml-3",
    },
    {
      Header: "Name",
      accessor: "name",
      Cell: (data) => {
        return (
          <div onClick={(e) => e.stopPropagation()}>
            <TextEditorCell
              {...data}
              iscellcomponent
              updateValue={(value) => {
                const { original } = data;
                const postdata = {
                  name: value || "",
                };
                const URL = `/client/user?id=${original.id}`;
                instance.put(URL, postdata).then(() => {
                  Toaster("Updated successfully", "success");
                });
              }}
            />
          </div>
        );
      },
    },
    {
      Header: "Department",
      accessor: "department",
      Cell: (data) => {
        return (
          <div onClick={(e) => e.stopPropagation()}>
            <TextEditorCell
              {...data}
              iscellcomponent
              updateValue={(value) => {
                const { original } = data;
                const postdata = {
                  department: value || "",
                };
                const URL = `/client/user?id=${original.id}`;
                instance.put(URL, postdata).then(() => {
                  Toaster("Updated successfully", "success");
                });
              }}
            />
          </div>
        );
      },
    },
    {
      Header: "Role",
      accessor: "roles",
      Cell: (data) => {
        return TokenManager.getUserId() == data.original.id ? (
          get(data, "value[0]", "") === "payadmin" ? (
            "admin"
          ) : (
            get(data, "value[0]", "")
          )
        ) : (
          <div onClick={(e) => e.stopPropagation()}>
            <SelectEditorCell
              {...data}
              value={get(data, "value[0]", "") === "payadmin" ? "admin" : get(data, "value[0]", "")}
              options={[
                { id: "admin", value: "admin", name: "Admin" },
                { id: "user", value: "user", name: "User" },
              ]}
              iscellcomponent
              updateValue={(value) => {
                if (value) {
                  const { original } = data;
                  const postdata = {
                    role: [value || ""],
                  };
                  const URL = `/client/user?id=${original.id}`;
                  instance.put(URL, postdata).then(() => {
                    Toaster("Updated successfully", "success");
                  });
                }
              }}
            />
          </div>
        );
      },
    },
    {
      Header: "Email",
      accessor: "email",
      // Cell: (data) => {
      //   return (
      //     <div onClick={(e) => e.stopPropagation()}>
      //       <TextEditorCell
      //         {...data}
      //         iscellcomponent
      //         updateValue={(value) => {
      //           const { original } = data;
      //           if (!emailRegEx.test(value)) {
      //             Toaster("Invalid email id", "error");
      //             return;
      //           }
      //           const postdata = {
      //             email: value || "",
      //           };
      //           const URL = `/client/user?id=${original.id}`;
      //           instance.put(URL, postdata).then(() => {
      //             Toaster("Updated successfully", "success");
      //           });
      //         }}
      //       />
      //     </div>
      //   );
      // },
    },
    {
      Header: () => <span className="ml-2"></span>,
      accessor: "id",
      width: 100,
      className: "overflow-unset",
      Cell: (data) =>
        TokenManager.getUserId() != data.value ? (
          <span>
            <i
              onClick={() => setselectedUser(data.original)}
              className="fa far fa-key color-blue cursour-pointer mx-2"
              title="Reset Password"
            />
            |
            <i
              onClick={() => {
                setconfirmationboxData({
                  variant: "warning",
                  msg: `Are you sure you want to remove ${get(data, "original.name", "")}?`,
                  onSave: () => {
                    resetconfirmationboxData();
                    instance.post(`client/user/delete?id=${data.value}`).then((resp) => {
                      getUser();
                      Toaster("Deleted successfully", "success");
                    });
                  },
                });
              }}
              className="fa fa-trash-o color-blue cursour-pointer mx-2"
              title="Remove user"
            />
          </span>
        ) : null,
    },
  ];

  return isAdmin ? (
    <>
      <Modal size="md" show={selectedUser} onHide={() => setselectedUser(null)}>
        <Modal.Header closeButton>Reset password for {get(selectedUser, "name", "")}</Modal.Header>
        <Modal.Body>
          <form onSubmit={update} className="d-flex justify-content-center row px-5">
            <div className="col-md-12">
              <div className="form-group">
                <span>
                  <label>New password</label>
                </span>
                <input
                  type="password"
                  name="newPassword"
                  className={`form-control ${showError && userPassword.newPassword && !passwordRegEx.test(userPassword.newPassword) ? "error" : ""}`}
                  placeholder="8 char, min 1 digit"
                  autoComplete="off"
                  onFocus={onChange}
                  onChange={onChange}
                  onBlur={onBlurr}
                  required
                  value={userPassword.newPassword}
                />
              </div>
            </div>
            <div className="col-md-12">
              <div className="form-group">
                <span>
                  <label>Confirm password</label>
                </span>
                <div className="confirm-password">
                  <input
                    type={showConfirmPassword ? "text" : "password"}
                    name="confirmPassword"
                    className={`form-control ${showError && userPassword.newPassword !== userPassword.confirmPassword ? "error" : ""}`}
                    placeholder="Enter ..."
                    autoComplete="off"
                    onFocus={onChange}
                    onChange={onChange}
                    onBlur={onBlurr}
                    required
                    value={userPassword.confirmPassword}
                    error={userPassword.newPassword}
                  />
                  <span
                    className="show-hide-toggle eye-password"
                    role="presentation"
                    onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                  >
                    {showConfirmPassword ? (
                      <i className="fa fa-eye" style={{ cursor: "pointer", fontSize: "15px" }} title="Show Password" />
                    ) : (
                      <i
                        className="fa fa-eye-slash"
                        style={{ cursor: "pointer", fontSize: "15px" }}
                        title="Show Password"
                      />
                    )}
                  </span>
                </div>
              </div>
            </div>
            <div className="col-md-12">
              <div className="form-group text-right" style={{ marginTop: "30px" }}>
                <button type="submit" className="btn subbtn btn-success">
                  {isProcessing ? (
                    <span>
                      Updating <i class="fa fa-spinner fa-spin" style={{ fontSize: "11px" }} aria-hidden="true"></i>
                    </span>
                  ) : (
                    <span>Update</span>
                  )}
                </button>
              </div>
            </div>
          </form>
        </Modal.Body>
      </Modal>
      <Accordion className="col-md-12" defaultActiveKey={expand === "#users" && "0"} id="users">
        <Accordion.Toggle
          className="border mt-4 px-3 cursour-pointer bg-light-grey"
          onClick={() => {
            setisexpanded(!isexpanded);
            isexpanded && getUser();
          }}
          as={Card.Header}
          eventKey="0"
        >
          <h6 className="my-0">
            Users
            {!addInProgress && (isexpanded || expand === "#users") && isAdmin && (
              <i
                onClick={(e) => {
                  e.stopPropagation();
                  setaddInProgress(true);
                }}
                className={`fa fa-plus cashinflowclick t-10 ${false ? "hide" : ""}`}
                title="click to add/invite user"
                aria-hidden="true"
              ></i>
            )}
          </h6>
        </Accordion.Toggle>
        <Accordion.Collapse className="p-2 px-3" eventKey="0">
          <form action method="post">
            <div className="px-0">
              {addInProgress && (
                <div className="row">
                  <div className="col-md-4">
                    <div className="form-group">
                      <span>
                        <label>Name</label>
                      </span>
                      <input
                        type="text"
                        name="name"
                        value={user.name}
                        onBlur={onBlurrNewUser}
                        onFocus={onChangeNewUser}
                        onChange={onChangeNewUser}
                        className="form-control"
                        placeholder="Enter ..."
                      />
                    </div>
                  </div>
                  <div className="col-md-4">
                    <div className="form-group">
                      <span>
                        <label>Department</label>
                      </span>
                      <input
                        type="text"
                        name="department"
                        value={user.department}
                        onBlur={onBlurrNewUser}
                        onFocus={onChangeNewUser}
                        onChange={onChangeNewUser}
                        className="form-control"
                        placeholder="Enter ..."
                      />
                    </div>
                  </div>
                  <div className="col-md-4">
                    <div className="form-group">
                      <span>
                        <label>Roles</label>
                      </span>
                      <select
                        className="form-control"
                        name="role"
                        value={user.role}
                        onBlur={onBlurrNewUser}
                        onFocus={onChangeNewUser}
                        onChange={onChangeNewUser}
                      >
                        <option selected disabled value="">
                          Select
                        </option>
                        <option value="admin">Admin</option>
                        <option value="user">User</option>
                      </select>
                    </div>
                  </div>
                  <div className="col-md-4">
                    <div className="form-group">
                      <span>
                        <label>Email</label>
                      </span>
                      <input
                        type="email"
                        name="email"
                        value={user.email}
                        onBlur={onBlurrNewUser}
                        onFocus={onChangeNewUser}
                        onChange={onChangeNewUser}
                        className="form-control"
                        placeholder="Enter ..."
                      />
                    </div>
                  </div>
                  <div className="col-md-4" />
                  <div className="col-md-4">
                    <div className="form-group text-right" style={{ marginTop: "30px" }}>
                      <button type="button" className="btn subbtn btn-primary mr-2" onClick={() => setaddInProgress(false)}>Cancel</button>
                      <button type="button" onClick={addUser} className="btn subbtn btn-success">
                        Invite User
                      </button>
                    </div>
                  </div>
                </div>
              )}

              <div className="row px-0">
                <div className="col-md-12 px-0">
                  <div className="paynewdetsectran tablemargin">
                    <ReactTable
                      noDataText={isFetching ? "" : "No data found"}
                      minRows={1}
                      loading={isFetching}
                      sortable={true}
                      resizable={true}
                      showPagination={false}
                      columns={columns}
                      data={userData}
                    />
                  </div>
                </div>
              </div>
            </div>
          </form>
        </Accordion.Collapse>
      </Accordion>
    </>
  ) : null;
}

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(User);
