import React from "react";
import { NativeTypes } from "react-dnd-html5-backend";
import CKEditor from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { useDrop } from "react-dnd";
const TextareaDropBox = props => {
  const { onDrop, charLimit, onTextChange, body } = props;
  const [{ canDrop, isOver }, drop] = useDrop({
    accept: [NativeTypes.FILE],
    drop(item, monitor) {
      if (onDrop) {
        onDrop({item, props, monitor});
      }
    },
    collect: monitor => ({
      isOver: monitor.isOver(),
      canDrop: monitor.canDrop()
    })
  });
  const isActive = canDrop && isOver;
  return (
    <div ref={drop} className="w-100" >
      {isActive ? (
        <div className="w-100 d-flex justify-content-center my-auto align-items-center" style={{ "height": "250px", "border": "1px dotted #ccc" }}>
          Release to drop
        </div>
      ) : (
          <CKEditor
            editor={ClassicEditor}
            data={body ? body : ""}
            config={{
              toolbar: ['heading', '|', 'bold', 'italic', 'numberedList', 'bulletedList']
            }}
            onChange={(event, editor) => {
              const data = editor.getData();
              onTextChange(data);
            }}
          />
      )}
    </div>
  );
};
export default TextareaDropBox;
