    componentDidMount() {
        this.getVendorCategory();
        const hash = this.props.location.hash;
        if (hash) {
            document.getElementById(hash.replace('#', '')).scrollIntoView(true);
        }
    }

    setshowExport = flag => {
        this.setState({ showexport: flag });
    }

    getVendorCategory = () => {
        this.setState({
            isVendorCategoryLoading: true,
        });
        instance
            .get(`vendorcategory?per_page=100`)
            .then(response => {
                this.setState({
                    vendorCategoryList: response.data.result.sort((a, b) => (+b.is_selected - +a.is_selected)),
                    isVendorCategoryLoading: false,
                })
            })
            .catch(e => handleAPIError(e));
    }

    onSubmit = () => {
        const { selectedData } = this.state;
        const URL = selectedData.id ? `vendorcategory?id=${selectedData.id}` : 'vendorcategory';
        const method = selectedData.id ? 'patch' : 'post';
        const requestObject = {
            name: selectedData.name,
            internal_id: Number(selectedData.internal_id),
            is_selected: selectedData.is_selected
        };

        if (!selectedData.internal_id) {
            delete requestObject.internal_id;
        }
        instance
        [method](URL, { ...requestObject})
            .then(() => {
                this.setState({
                    line: '',
                    selectedData: null,
                });
                this.getVendorCategory();
                localStorage.setItem("refresh_reports", true);
            })
            .catch(e => handleAPIError(e));
    };

    onDelete = (id) => {
        instance
            .delete(`vendorcategory?id=${id}`)
            .then(() => {
                this.getVendorCategory();
                localStorage.setItem("refresh_reports", true);
            })
            .catch(e => handleAPIError(e));
    }

