import React, { Component } from "react";
import ReactTable from "react-table-v6";
import PlaidLink from "react-plaid-link";
import axios from "axios";
import { instance } from "actions/axiosInstance";
import get from "lodash/get";
import { Modal, Button } from "react-bootstrap";
import Toaster from "./Toaster";
import fe_config from "constants/Configs";

class CustomPlaidLink extends Component {
  constructor() {
    super();

    this.state = {
      allaccounts: [],
      showModal: false
    };

    this.handleClick = this.handleClick.bind(this);
    this.handleOnSuccess = this.handleOnSuccess.bind(this);
    this.handleOnSuccess = this.handleOnSuccess.bind(this);
  }

  handleOnSuccess(public_token, metadata) {
    const postdata = {
      public_token,
      accounts: metadata.accounts.map(x => ({
        account_id: x.id,
        name: `${get(metadata, "institution.name", "")} - ${x.name}`
      }))
    };
    instance
      .put("bankaccount", postdata)
      .then(() => {
        Toaster("Accounts Added successfully", "success");
        this.setState({ showModal: false });
        this.props.getAccount && this.props.getAccount();
      });

    // send token to client server
    // instance
    //   .get(`plaid/accounts?public_token=${public_token}`, {
    //     public_token: public_token
    //   })
    //   .then(resp => {
    //     this.props.getAccount();
    //     // this.setState({ public_token, allaccounts: get(resp, "data.accounts.accounts", []), showModal: true });
    //   });
  }

  saveAccounts = () => {
    const { public_token } = this.state;
    const selectedAccounts = get(this.state, "allaccounts", []).filter(x => x.isselected);
    if (!get(selectedAccounts, "[0]", "")) {
      return;
    }
    const postdata = {
      public_token,
      accounts: selectedAccounts.map(x => ({
        account_id: x.account_id,
        name: x.name
      }))
    };
    instance
      .put("bankaccount", postdata)
      .then(() => {
        Toaster("Accounts Added successfully", "success");
        this.setState({ showModal: false });
      });
  };

  handleOnExit() {
    // handle the case when your user exits Link
    // For the sake of this tutorial, we're not going to be doing anything here.
  }

  handleClick(res) {
    axios.get("/transactions").then(res => {
      this.setState({ transactions: res.data });
    });
  }

  setallaccounts = acc => {
    this.setState({ allaccounts: acc });
  };

  render() {
    const { showModal, allaccounts } = this.state;

    const columns = [
      {
        Header: (
          <input
            className="md-checkbox bg-white"
            type="checkbox"
            onChange={() => {
              const newvalue = !(allaccounts && !allaccounts.find(x => !x.isselected));
              this.setallaccounts(allaccounts.map(x => ({ ...x, isselected: newvalue })));
            }}
            checked={get(allaccounts, "[0]", "") && !allaccounts.find(x => !x.isselected)}
          />
        ),
        accessor: "isselected",
        Cell: props => (
          <div
            className=" bg-white text-secondary"
            onClick={e => {
              e.stopPropagation();
              const newvalue = !props.value;
              const updatedallaccounts = [...allaccounts];
              updatedallaccounts[props.index]["isselected"] = newvalue;
              this.setallaccounts(updatedallaccounts);
            }}
          >
            <input
              className="md-checkbox"
              style={{ marginLeft: 15 }}
              type="checkbox"
              defaultChecked={props.value}
              checked={props.value}
            />
          </div>
        ),
        sortable: false,
        filterable: false,
        width: 50
      },
      {
        Header: "Select all",
        accessor: "name",
        Cell: data => get(data, "value", "")
      }
    ];

    return (
      <div className="mt-2 p-1">
        <Modal show={showModal} size="md" onHide={() => this.setState({ showModal: false })} className="mt-4">
          <Modal.Header closeButton>
            <Modal.Title className="h6">Select Accounts</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <ReactTable
              noDataText="No data found"
              minRows={allaccounts.length > 10 ? 10 : allaccounts.length}
              resizable={true}
              showPagination={false}
              // getTrProps={(state, rowInfo, column) => ({
              //   onClick: e => {
              //     window.open(`${window.location.origin}/payables/${get(rowInfo.original, "id", "-")}`, "_self");
              //   }
              // })}
              columns={columns}
              data={allaccounts || []}
            />
          </Modal.Body>
          <Modal.Footer closeButton>
            <Button onClick={this.saveAccounts}>Connect</Button>
          </Modal.Footer>
        </Modal>

        <PlaidLink
          clientName="React Plaid Setup"
          env={fe_config.PLAID_env}
          product={["transactions"]}
          publicKey={fe_config.PLAID_publicKey}
          onExit={this.handleOnExit}
          onSuccess={this.handleOnSuccess}
          classNaCustomPlaidLinkme="test"
          className="width-200px"
        >
          Connect Bank Account
        </PlaidLink>
        {/* <div>
          <button onClick={this.handleClick}>Get Transactions</button>
        </div> */}
      </div>
    );
  }
}

export default CustomPlaidLink;
