import React, { useEffect } from "react";
import { connect } from "react-redux";
import { Accordion, Card } from "react-bootstrap";

function Notification(props) {
  const expand = props.location.hash;
  useEffect(() => {
    if (['#notifications'].includes(expand)) {
      document.getElementById("notifications").scrollIntoView()
    }
  }, []);
  const update = e => {
    e.preventDefault();
  };

  return (
    <Accordion className="col-md-12" id="notifications" defaultActiveKey={expand === '#notifications' && "0"} >
      <Accordion.Toggle className="border mt-4 px-3 cursour-pointer bg-light-grey" as={Card.Header} eventKey="0">
        <h6 className="my-0">Notification Details</h6>
      </Accordion.Toggle>
      <Accordion.Collapse className="p-2 px-3 pt-3" eventKey="0">
        <form action method="post">
          <div className="row">
            <div className="col-md-4">
              <div className="managecheckbox">
                <label className="container">
                  once daily
                  <input type="checkbox" defaultChecked="checked" />
                  <span className="checkmark" />
                </label>
              </div>
            </div>
            <div className="col-md-4">
              <div className="managecheckbox">
                <label className="container">
                  When payment comes in account
                  <input type="checkbox" />
                  <span className="checkmark" />
                </label>
              </div>
            </div>
            <div className="col-md-4">
              <div className="form-group text-right">
                <button type="submit" onClick={update} className="btn subbtn btn-success">
                  Update
                </button>
              </div>
            </div>
          </div>
        </form>
      </Accordion.Collapse>
    </Accordion>
  );
}

const mapStateToProps = state => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(Notification);
