import React, { useRef, useState }  from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import uniqBy from "lodash/uniqBy";
import debounce from "lodash/debounce";
import Autocomplete from "components/Autocomplete";
import logo from "../assets/images/logo.jpg";
import ClickAwayListener from "./ClickAwayListener";
import TokenManager from "utils/TokenManager";
import { logout } from "handlers/setters";
import { setNavigation } from "actions/global";
import { instanceWithRetry } from "actions/axiosInstance";
import { handleAPIError } from "handlers/setters";

export const Header = ({ openleftnav, setNavigation, history }) => {
  const [showpopup, setshowpopup] = useState(false);
  const [searchList, setSearchList] = useState([]);
  const [isSearchFetching, setIsSearchFetching] = useState(false);
  const username = TokenManager.getUserName() || "-";
  const debounceMethod = useRef(debounce((text) => search(text), 500)).current;

  const search = (text) => {
    const url = `search?per_page=100&search_text=${text}`;
    if (text) {
      setIsSearchFetching(true);
      setSearchList([]);
      instanceWithRetry
        .get(url)
        .then((res) => {
          setIsSearchFetching(false);
          const data = res.data.result;
          // Updating order to Customer, Vendor, Receivables, Payables, Order backlog
          const final = data.sort((a, b) => {
            if(a.model_type === 'Customer') {
              return -1;
            } else if (a.model_type === 'Vendor' && b.model_type !== 'Customer') {
              return -1;
            } else if (a.model_type === 'AR_Invoice' && b.model_type !== 'Vendor' && b.model_type !== 'Customer') {
              return -1;
            } else if (a.model_type === 'AP_Invoice' && b.model_type !== 'Vendor' && b.model_type !== 'Customer' && b.model_type !== 'AR_Invoice') {
              return -1;
            }
            return 1;
          });
          setSearchList(final);
        })
        .catch((e) => handleAPIError(e));
    } else {
      setSearchList([]);
    }
  };

  const routes = {
    'AP_Invoice': 'Payables',
    'AR_Invoice': 'Receivables',
    'Customer': 'Customers',
    'Vendor': 'Vendors',
    'OrderBacklog': 'Backlog'
  };

  return (
    <div className="row menufixed" id="topHeader">
      <div className="logop-expanded switchButton">
        <div data-toggle="sidebar-colapse" onClick={() => { setNavigation({ openleftnav: !openleftnav }); }}>
          <i id="swit" className={`fas fa-fw mr-3 ${openleftnav ? "fa-chevron-left" : "fa-chevron-right"} cursour-pointer`}></i>{" "}
        </div>
        <img className="logo" src={logo} alt="loading" />
        Payference
      </div>
      <div className="col mp-0">
        <div className="row mp-0">
          <div className="col-md-8">
            <form className="navbar-form navbar-left searchform">
              <div className="input-group">
                <Autocomplete
                  suggestions={uniqBy(
                    searchList.map((s) => ({
                      id: s.id,
                      type: s.model_type,
                      name: `<strong>${s.model_type === 'OrderBacklog' ? 'Order ': ''}${routes[s.model_type]}:</strong> ${s.display_string}`,
                    })),
                    "id"
                  )}
                  placeholder="Search for an invoice, customer or vendor"
                  inputClassName="searchbar"
                  inputWrapperClassName="search-wrapper"
                  getSelected={(item) => {if(item) {setSearchList([]); history.push(`/${routes[item.type].toLowerCase()}/${item.id}`)}}}
                  search={(text) => debounceMethod(text)}
                  isLoading={isSearchFetching}
                />
              </div>
            </form>
          </div>
          <div className="col-md-4">
            <div className="pull-right">
              <div className="dropdown">
                <div
                  className="btn cursour-pointer" // dropdown-toggle for down arrow icon
                  to="#"
                  role="button"
                  id="profile"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                  onClick={() => setshowpopup(!showpopup)}
                >
                  <div className="text-icon-big rounded-circle">{username[0]}</div>
                </div>
                <ClickAwayListener onClickAway={setshowpopup}>
                  <div
                    className={`dropdown-menu ${showpopup && "open-profilepic-menu show"}`}
                    id="profilebox"
                    aria-labelledby="dropdownMenuLink"
                  >
                    <div className="text-icon-big rounded-circle">{username[0]}</div>
                    <p title={username} className="text-center">{username}</p>
                    <div className="row" id="dtwrapper">
                      <div className="col-md-6">
                        <Link onClick={() => setshowpopup(false)} to="/settings/profile">
                          Profile
                        </Link>
                      </div>
                      <div className="col-md-6 align-right">
                        <Link
                          disabled
                          onClick={() => {
                            setshowpopup(false);
                            logout();
                          }}
                        >
                          Signout
                        </Link>
                      </div>
                    </div>
                  </div>
                </ClickAwayListener>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = state => ({
  openleftnav: state.navigation.openleftnav
});

function mapDispatchToProps(dispatch) {
  return bindActionCreators({ setNavigation }, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Header);
