import React, { Component } from "react";
import CKEditor from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { Button, Modal } from "react-bootstrap";
import get from "lodash/get";

export default class EmailTemplateModal extends Component {
  state = {
    requestObject: null
  };

  getSnapshotBeforeUpdate(prevProps, prevState) {
    if (prevProps.defaultData !== this.props.defaultData) {
      return this.props.defaultData;
    }
    return null;
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (snapshot !== null) {
      this.setState({
        requestObject: {
          ...snapshot,
        }
      })
    }
  }

  render() {
    const { show, mode = "add", handleModal, defaultData, submitEntry } = this.props;
    const { requestObject } = this.state;

    return (
      <Modal show={show} onHide={handleModal} className="mt-5" size="lg" backdrop="static">
        <Modal.Header closeButton>{mode === "add" ? "New" : "Update"} Template</Modal.Header>
        <Modal.Body style={{ 'max-height': 'calc(100vh - 210px)', 'overflow-y': 'auto' }}>
          <div className="mb-3">
            <strong>Template Name</strong>
            <input
              type="text"
              className="form-control"
              defaultValue={defaultData ? defaultData.name : ""}
              onChange={e =>
                this.setState({
                  requestObject: {
                    ...this.state.requestObject,
                    name: e.target.value
                  }
                })
              }
            />
          </div>
          <div className="mb-3">
            <strong>Subject</strong>
            <input
              type="text"
              className="form-control"
              defaultValue={defaultData ? defaultData.subject : ""}
              onChange={e =>
                this.setState({
                  requestObject: {
                    ...this.state.requestObject,
                    subject: e.target.value
                  }
                })
              }
            />
          </div>
          <div>
            <label>
              <strong>Body:</strong>
            </label>
          </div>
          <CKEditor
            editor={ClassicEditor}
            data={defaultData ? defaultData.body : ""}
            config={{
              toolbar: ['heading', '|', 'bold', 'italic', 'numberedList', 'bulletedList']
            }}
            onChange={(event, editor) => {
              const data = editor.getData();
              this.setState({
                requestObject: {
                  ...this.state.requestObject,
                  body: data
                }
              });
            }}
          />
          <div className="mt-3">
            <Button
              disabled={
                !(get(requestObject, "name", "") && get(requestObject, "subject", "") && get(requestObject, "body", ""))
              }
              onClick={() => {
                handleModal();
                submitEntry(mode, this.state.requestObject, defaultData);
              }}
            >
              Save
            </Button>
          </div>
        </Modal.Body>
      </Modal>
    );
  }
}
