import React from "react";
import EmailTemplate from "./EmailTemplate";
import { instance } from "../../../actions/axiosInstance";
import "../settings.css";
import parse from 'html-react-parser';
import isEqual from "lodash/isEqual";
import { setconfirmationboxData, resetconfirmationboxData } from "components/ConfirmationBox";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import Toaster from "components/Toaster";
import Autocomplete from "components/Autocomplete";
import uniqBy from "lodash/uniqBy";

const initialState = {
  days: "",
  action: "",
  period: null,
  editLine: "",
  customer_id: "",
  template_id: "",
  reminderType: "-1",
  is_recurring: false,
  ar_age_in_days: null,
  frequency_in_days: null,
  currentPage: 1,
  editableData: null,
  showEmailModal: false,
  requestObject: null,
  corporate_entity_id: "",
  is_enabled: false,
  selectedCustomer: null,
}
export default class CustomTable extends React.Component {
  state = {
    ...initialState,
  };

  resetState = () => this.setState({ ...initialState });

  handleEmailModal = () =>
    this.setState(
      prevState => ({
        showEmailModal: !prevState.showEmailModal
      }),
      () =>
        this.setState(prevState => ({
          editableData: prevState.showEmailModal === false ? null : prevState.editableData
        }))
    );

  paginate = (array, page_size, page_number) => array.slice((page_number - 1) * page_size, page_number * page_size);

  addNewRow = () => {
    const { type, companyList, customerList, emailTemplateList, isCustomerListLoading, searchCustomer } = this.props;

    const { editableData, days, reminderType, template_id, corporate_entity_id, customer_id, selectedCustomer } = this.state;

    const dummyArray = new Array(7).fill().map((v, i) => i + 1);

    const dropdownData = type === "customer" ? customerList : companyList;

    if (this.state.action === "add") {
      return (
        <tr>
          <td>
            {
              type === "customer" ?
                <Autocomplete
                  suggestions={uniqBy(
                    customerList.map(s => ({ id: s.customer.id, name: s.customer.name })),
                    "id"
                  )}
                  getSelected={item => item ? this.setState({ customer_id: item.id, selectedCustomer: item }) : this.setState({ customer_id: '', selectedCustomer: null })}
                  search={text => searchCustomer(text)}
                  isLoading={isCustomerListLoading}
                  defaultValue={selectedCustomer && selectedCustomer.name}
                />
                :
                <select
                  className="form-control"
                  onChange={e =>
                    this.setState({
                      [type === "customer" ? "customer_id" : "corporate_entity_id"]: e.target.value
                    })
                  }
                >
                  <option disabled selected>
                    Please select
                  </option>
                  {dropdownData.length
                    ? dropdownData.map(d => (
                        <option key={d.id} value={d.id}>
                          {d.name}
                        </option>
                      ))
                    : null}
                </select>
            }
          </td>
          <td>
            <select
              className="form-control"
              onChange={e =>
                this.setState({
                  reminderType: e.target.value
                })
              }
            >
              {/* <option disabled selected>
                Please select
              </option> */}
              <option value="-1" selected>
                before
              </option>
              <option value="1">after</option>
            </select>
          </td>
          <td>
            <input
              type="number"
              className="form-control"
              onInput={e =>
                this.setState({
                  days: e.target.value
                })
              }
            />
          </td>
          <td>
            <select
              className="form-control"
              onChange={e =>
                this.setState({
                  template_id: e.target.value
                })
              }
            >
              <option disabled selected>
                Please select
              </option>
              {emailTemplateList.length
                ? emailTemplateList.map(d => (
                    <option key={d.id} value={d.id}>
                      {d.name}
                    </option>
                  ))
                : null}
            </select>
          </td>
          <td>
            <select className="form-control"
              onChange={e =>
                this.setState({
                  period: e.target.value
                })
              }>
              <option disabled selected>
                Please select
                </option>
              {
                dummyArray.map(d => <option value={d}>Every {d} days</option>)
              }
            </select>
          </td>
          <td>
            <div className="checkbox switcher">
              <label htmlFor={`toggle-${type}-new`}>
                <input
                  type="checkbox"
                  id={`toggle-${type}-new`}
                  onChange={e => {
                    e.stopPropagation();
                    this.setState({ is_enabled: !this.state.is_enabled });
                  }}
                />
                <span>
                  <small></small>
                </span>
              </label>
            </div>
          </td>
          <td>
            <button
              disabled={!days || !reminderType || !template_id || (!customer_id && !corporate_entity_id)}
              title="Save"
              className="btn color-blue pull-left p-2"
              onClick={e => {
                e.stopPropagation();
                this.submitEntry("add", null, type);
                this.resetState();
              }}
            >
              <i className="fas fa-save" />
            </button>
            <span>|</span>
            <button
              title="Cancel"
              className="btn color-blue p-2"
              onClick={e => {
                e.stopPropagation();
                this.resetState();
              }}
            >
              <i className="fas fa-remove" />
            </button>
          </td>
        </tr>
      );
    } else if (this.state.action === "edit") {
      return (
        <ClickAwayListener
          onClickAway={e => {
            e.stopPropagation();
            this.submitEntry("edit", editableData, type);
            this.resetState();
          }}
        >
          <tr>
            <td>
              {
                type === "customer" ?
                  <Autocomplete
                    suggestions={uniqBy(
                      customerList.map(s => ({ id: s.customer.id, name: s.customer.name })),
                      "id"
                    )}
                    getSelected={item => item ? this.setState({ customer_id: item.id, selectedCustomer: item }) : this.setState({ customer_id: '', selectedCustomer: null })}
                    search={text => searchCustomer(text)}
                    isLoading={isCustomerListLoading}
                    defaultValue={selectedCustomer && selectedCustomer.name}
                  />
                  :
                <select
                  className="form-control"
                  defaultValue={editableData[type === "customer" ? "customer_id" : "corporate_entity_id"]}
                  onChange={e =>
                    this.setState({
                      [type === "customer" ? "customer_id" : "corporate_entity_id"]: e.target.value
                    })
                  }
                >
                  <option disabled selected>
                    Please select
                  </option>
                  {dropdownData.length
                    ? dropdownData.map(d => (
                        <option key={d.id} value={d.id}>
                          {d.name}
                        </option>
                      ))
                    : null}
                </select>
              }
            </td>
            <td>
              <select
                className="form-control"
                defaultValue={reminderType}
                onChange={e =>
                  this.setState({
                    reminderType: e.target.value
                  })
                }
              >
                {/* <option disabled selected>
                Please select
              </option> */}
                <option value="-1" selected>
                  before
                </option>
                <option value="1">after</option>
              </select>
            </td>
            <td>
              <input
                type="number"
                className="form-control"
                defaultValue={Math.abs(editableData.days)}
                onInput={e =>
                  this.setState({
                    days: e.target.value
                  })
                }
              />
            </td>
            <td>
              <select
                className="form-control"
                defaultValue={Number(editableData.template_id)}
                onChange={e =>
                  this.setState({
                    template_id: e.target.value
                  })
                }
              >
                <option disabled selected>
                  Please select
                </option>
                {emailTemplateList.length
                  ? emailTemplateList.map(d => (
                      <option key={d.id} value={d.id}>
                        {d.name}
                      </option>
                    ))
                  : null}
              </select>
            </td>
            <td>
              <select defaultValue={editableData.period} className="form-control"
                onChange={e =>
                  this.setState({
                    period: e.target.value
                  })
                }>
                <option disabled selected>
                  Please select
                </option>
                {
                  dummyArray.map(d => <option value={d}>Every {d} days</option>)
                }
              </select>
            </td>
            <td>
              <div className="checkbox switcher">
                <label
                  htmlFor={`toggle-${type}-${
                    type === "customer" ? editableData.customer_id : editableData.corporate_entity_id
                  }`}
                >
                  <input
                    type="checkbox"
                    id={`toggle-${type}-${
                      type === "customer" ? editableData.customer_id : editableData.corporate_entity_id
                    }`}
                    defaultChecked={editableData.is_enabled}
                    onChange={e => {
                      e.stopPropagation();
                      this.setState({ is_enabled: !this.state.is_enabled });
                    }}
                  />
                  <span>
                    <small></small>
                  </span>
                </label>
              </div>
            </td>
            <td>
              <button
                title="Cancel"
                className="btn color-blue p-2"
                onClick={e => {
                  e.stopPropagation();
                  this.resetState();
                }}
              >
                <i className="fas fa-remove" />
              </button>
            </td>
          </tr>
        </ClickAwayListener>
      );
    }
  };

  getName = (id, type) => {
    const { companyList, emailTemplateList, customerListNames } = this.props;
    if (['customer', 'reccustomer'].includes(type) && customerListNames && customerListNames.length) {
      const customer = customerListNames.find(d => d.id === id);
      if(customer) {
        return customer.name;
      } else {
        return '-';
      }
    } else if (['company', 'reccompany'].includes(type) && companyList.length) {
      const entity = companyList.find(d => d.id === id);
      return entity.name;
    } else if (type === "email" && emailTemplateList && emailTemplateList.length) {
      const template = emailTemplateList.find(d => d.id === id);
      return template.name;
    }
    return "-";
  };

  renderRows = (type, data) => {
    const { editableData, editLine } = this.state;
    if (type === "customer" || type === "company") {
      return data.map((d, line) => {
        if (!editableData || (editableData && editableData.id !== d.id)) {
          if (line === editLine) {
            this.addNewRow();
          } else {
            return (
              <tr
                onClick={() =>
                  this.setState({
                    action: "edit",
                    editableData: d,
                    reminderType: d.days < 0 ? "-1" : "1",
                    editLine: line,
                    is_enabled: d.is_enabled,
                    selectedCustomer: { name: this.getName(d.customer_id, 'customer')}
                  })
                }
              >
                <td>
                  {type === "customer" ? this.getName(d.customer_id, type) : this.getName(d.corporate_entity_id, type)}
                </td>
                <td>{d.days < 0 ? "before" : "after"}</td>
                <td>{d.days && Math.abs(d.days)}</td>
                <td>{this.getName(d.template_id, "email")}</td>
                <td>
                  {d.period ? `Every ${d.period} days` : '-'}
                </td>
                <td onClick={e => e.stopPropagation()}>
                  <div className="checkbox switcher">
                    <label htmlFor={`toggle-${type}-${d.id}`}>
                      <input
                        type="checkbox"
                        id={`toggle-${type}-${d.id}`}
                        defaultChecked={d.is_enabled}
                        onChange={e => {
                          const URL = `receivable-reminder?id=${d.id}`;
                          instance
                            .patch(
                              URL,
                              {
                                [type === "customer" ? "customer_id" : "corporate_entity_id"]:
                                  type === "customer" ? d.customer_id : d.corporate_entity_id,
                                is_enabled: e.target.checked
                              }
                            )
                            .then(response => {
                              this.props.reloadListing(type);
                              this.resetState();
                              Toaster("Updated successfully", "success");
                            });
                        }}
                      />
                      <span>
                        <small></small>
                      </span>
                    </label>
                  </div>
                </td>
                <td onClick={e => e.stopPropagation()}>
                  <button
                    title="Delete"
                    className="btn color-blue p-2"
                    onClick={e => {
                      e.stopPropagation();

                      setconfirmationboxData({
                        variant: "warning",
                        msg: "Are you sure you want to delete this record?",
                        onSave: () => {
                          resetconfirmationboxData();
                          this.deleteEntry(d.id, type);
                        }
                      });
                    }}
                  >
                    <i className="fa fa-trash-o"></i>
                  </button>
                </td>
              </tr>
            );
          }
        }
        return this.addNewRow();
      });
    }
    if (type === "email") {
      return data.map((d, line) => (
        <tr onClick={() => this.setState({ showEmailModal: true, editableData: d })}>
          <td>{d.id}</td>
          <td>{d.name}</td>
          <td>{d.subject}</td>
          <td><div className="emailbody">{parse(d.body.trim())}</div></td>
          <td>
            <button
              title="Delete"
              className="btn color-blue p-2"
              onClick={e => {
                e.stopPropagation();
                setconfirmationboxData({
                  variant: "warning",
                  msg: "Are you sure you want to delete this record?",
                  onSave: () => {
                    resetconfirmationboxData();
                    this.deleteEntry(d.id, type);
                  }
                });
              }}
            >
              <i className="fa fa-trash-o"></i>
            </button>
          </td>
        </tr>
      ));
    }
  };

  handleAddNewRow = type => {
    if (['customer', 'reccustomer', 'company', 'reccompany'].includes(type)) {
      this.setState({ action: "add", selectedCustomer: null });
    } else {
      this.setState({ showEmailModal: true });
    }
  };

  handlePagination = action =>
    this.setState({
      currentPage: action === "next" ? this.state.currentPage + 1 : this.state.currentPage - 1
    });

  submitEntry = (mode, prevData, type) => {
    const URL = mode === "add" ? "receivable-reminder" : `receivable-reminder?id=${prevData.id}`;
    const requestType = mode === "add" ? "post" : "patch";
    const { days, template_id, reminderType, is_enabled, period, is_recurring, ar_age_in_days, frequency_in_days } = this.state;
    let requestObject = {
      [['customer', 'reccustomer'].includes(type) ? "customer_id" : "corporate_entity_id"]: this.state[
        ['customer', 'reccustomer'].includes(type) ? "customer_id" : "corporate_entity_id"
      ],
      days,
      period,
      template_id,
      is_enabled,
      is_recurring,
      ar_age_in_days,
      frequency_in_days,
    };
    if (mode === "add") {
      const days = reminderType === "-1" ? Number(requestObject.days) * -1 : Number(requestObject.days);
      requestObject.days = days;
    }
    if (mode === "edit") {
      requestObject = {
        ...prevData,
        [['customer', 'reccustomer'].includes(type) ? "customer_id" : "corporate_entity_id"]: prevData[
          ['customer', 'reccustomer'].includes(type) ? "customer_id" : "corporate_entity_id"
        ],
      };
      if(is_recurring) {
        requestObject.is_recurring = true;
        requestObject.is_enabled = true;
      }
      if(is_enabled) {
        requestObject.is_enabled = true;
      }
      if (period) {
        requestObject.period = period;
      }
      if(ar_age_in_days) {
        requestObject.ar_age_in_days = ar_age_in_days;
      }
      if (frequency_in_days) {
        requestObject.frequency_in_days = frequency_in_days;
      }
      if (this.state[['customer', 'reccustomer'].includes(type) ? "customer_id" : "corporate_entity_id"]) {
        requestObject[['customer', 'reccustomer'].includes(type) ? "customer_id" : "corporate_entity_id"] = this.state[
          ['customer', 'reccustomer'].includes(type) ? "customer_id" : "corporate_entity_id"
        ];
      }
      if (template_id) {
        requestObject.template_id = template_id;
      }
      requestObject.days = Math.abs(days ? days : prevData.days) * Number(reminderType);

      // if (days && reminderType) {
      //   requestObject.days = Number(days);
      // }
      // if (reminderType && days) {
      //   requestObject.days = reminderType === "-1" ? Number(days) * -1 : Number(days);
      // }
    }
    if (isEqual(prevData, requestObject)) {
      return;
    }
    instance[requestType](
      URL,
      { ...requestObject }
    ).then(response => {
      this.props.reloadListing(type);
      this.resetState();
      const msg = mode === "add" ? "Added" : "Updated";
      Toaster(msg + " successfully", "success");
    });
  };

  submitEmailTemplateEntry = (mode, requestObject, prevData) => {
    const URL =
      mode === "add" ? "receivable-reminder-email-template" : `receivable-reminder-email-template?id=${prevData.id}`;
    const requestType = mode === "add" ? "post" : "patch";

    instance[requestType](
      URL,
      { ...requestObject }
    ).then(response => {
      this.props.reloadListing("email");
      this.resetState();
    });
  };

  deleteEntry = (id, type) => {
    const URL = type === "email" ? `receivable-reminder-email-template?id=${id}` : `receivable-reminder?id=${id}`;
    instance
      .delete(URL)
      .then(() => {
        Toaster("Deleted successfully", "success");
        this.props.reloadListing(type);
      })
      .catch(e => {
        if(type === 'email') {
          Toaster("This template is in use", "error");
        }
      });
  };

  renderRecurranceTable = (type, data) => {
    const { editLine } = this.state;
    return data.map((d, line) => {
      if (line === editLine) {
        return this.addNewRecurranceRow();
      }
      return (
        <tr onClick={() => this.setState({ editableData: d, editLine: line, action: 'edit', selectedCustomer: { name: this.getName(d.customer_id, 'customer') } })}>
          <td>
            {type === "reccustomer" ? this.getName(d.customer_id, type) : this.getName(d.corporate_entity_id, type)}
          </td>
          <td>{d.ar_age_in_days}</td>
          <td>{d.frequency_in_days}</td>
          <td>{this.getName(d.template_id, "email")}</td>
          <td onClick={e => e.stopPropagation()}>
            <div className="checkbox switcher">
              <label htmlFor={`toggle-${type}-${d.id}`}>
                <input
                  type="checkbox"
                  id={`toggle-${type}-${d.id}`}
                  defaultChecked={d.is_recurring}
                  onChange={e => {
                    const URL = `receivable-reminder?id=${d.id}`;
                    instance
                      .patch(
                        URL,
                        {
                          [type === "customer" ? "customer_id" : "corporate_entity_id"]:
                            type === "customer" ? d.customer_id : d.corporate_entity_id,
                          is_recurring: e.target.checked,
                          is_enabled: e.target.checked,
                        }
                      )
                      .then(response => {
                        this.props.reloadListing(type);
                        this.resetState();
                        Toaster("Updated successfully", "success");
                      });
                  }}
                />
                <span>
                  <small></small>
                </span>
              </label>
            </div>
          </td>
          <td>
            <button
              title="Delete"
              className="btn color-blue p-2"
              onClick={e => {
                e.stopPropagation();
                setconfirmationboxData({
                  variant: "warning",
                  msg: "Are you sure you want to delete this record?",
                  onSave: () => {
                    resetconfirmationboxData();
                    this.deleteEntry(d.id, type);
                  }
                });
              }}
            >
              <i className="fa fa-trash-o"></i>
            </button>
          </td>
        </tr>
      )});
  };

  addNewRecurranceRow = () => {
    const { type, companyList, customerList, emailTemplateList, isCustomerListLoading, searchCustomer } = this.props;

    const { editableData, selectedCustomer, action, ar_age_in_days, frequency_in_days, template_id, customer_id, corporate_entity_id } = this.state;

    const dropdownData = type === "customer" ? customerList : companyList;
    if(action === 'add') {
      return (
        <tr>
          <td>
            {
              type === "reccustomer" ?
                <Autocomplete
                  suggestions={uniqBy(
                    customerList.map(s => ({ id: s.customer.id, name: s.customer.name })),
                    "id"
                  )}
                  getSelected={item => item ? this.setState({ customer_id: item.id, selectedCustomer: item }) : this.setState({ customer_id: '', selectedCustomer: null })}
                  search={text => searchCustomer(text)}
                  isLoading={isCustomerListLoading}
                  defaultValue={selectedCustomer && selectedCustomer.name}
                />
                :
                <select
                  className="form-control"
                  onChange={e =>
                    this.setState({
                      [type === "reccustomer" ? "customer_id" : "corporate_entity_id"]: e.target.value
                    })
                  }
                >
                  <option disabled selected>
                    Please select
                </option>
                  {dropdownData.length
                    ? dropdownData.map(d => (
                      <option key={d.id} value={d.id}>
                        {d.name}
                      </option>
                    ))
                    : null}
                </select>
            }
          </td>
          <td>
            <select
              className="form-control"
              onChange={e =>
                this.setState({
                  'ar_age_in_days': e.target.value
                })
              }
            >
              <option disabled selected>Please select</option>
              {
                [15, 30, 60, 90].map(d => (
                  <option key={d} value={d}>
                    {d}
                  </option>
                ))
              }
            </select>
          </td>
          <td>
            <input
              type="number"
              className="form-control"
              onInput={e =>
                this.setState({
                  frequency_in_days: e.target.value
                })
              }
            />
          </td>
          <td>
            <select
              className="form-control"
              onChange={e =>
                this.setState({
                  template_id: e.target.value
                })
              }
            >
              <option disabled selected>
                Please select
              </option>
              {emailTemplateList.length
                ? emailTemplateList.map(d => (
                  <option key={d.id} value={d.id}>
                    {d.name}
                  </option>
                ))
                : null}
            </select>
          </td>
          <td>
            <div className="checkbox switcher">
              <label htmlFor={`toggle-${type}-new`}>
                <input
                  type="checkbox"
                  id={`toggle-${type}-new`}
                  onChange={e => {
                    e.stopPropagation();
                    this.setState({ is_recurring: !this.state.is_recurring, is_enabled: !this.state.is_recurring });
                  }}
                />
                <span>
                  <small></small>
                </span>
              </label>
            </div>
          </td>
          <td>
            <button
              disabled={!ar_age_in_days || !frequency_in_days || !template_id || (!customer_id && !corporate_entity_id)}
              title="Save"
              className="btn color-blue pull-left p-2"
              onClick={e => {
                e.stopPropagation();
                this.submitEntry("add", null, type);
                this.resetState();
              }}
            >
              <i className="fas fa-save" />
            </button>
            <span>|</span>
            <button
              title="Cancel"
              className="btn color-blue p-2"
              onClick={e => {
                e.stopPropagation();
                this.resetState();
              }}
            >
              <i className="fas fa-remove" />
            </button>
          </td>
        </tr>
      );
    }
    if(action === 'edit') {
      return (
        <ClickAwayListener
          onClickAway={e => {
            e.stopPropagation();
            this.submitEntry("edit", editableData, type);
            this.resetState();
          }}
        >
          <tr>
            <td>
              {
                type === "reccustomer" ?
                  <Autocomplete
                    suggestions={uniqBy(
                      customerList.map(s => ({ id: s.customer.id, name: s.customer.name })),
                      "id"
                    )}
                    getSelected={item => item ? this.setState({ customer_id: item.id, selectedCustomer: item }) : this.setState({ customer_id: '', selectedCustomer: null })}
                    search={text => searchCustomer(text)}
                    isLoading={isCustomerListLoading}
                    defaultValue={selectedCustomer && selectedCustomer.name}
                  />
                  :
                  <select
                    className="form-control"
                    defaultValue={editableData[type === "reccustomer" ? "customer_id" : "corporate_entity_id"]}
                    onChange={e =>
                      this.setState({
                        [type === "reccustomer" ? "customer_id" : "corporate_entity_id"]: e.target.value
                      })
                    }
                  >
                    <option disabled selected>
                      Please select
                  </option>
                    {dropdownData.length
                      ? dropdownData.map(d => (
                        <option key={d.id} value={d.id}>
                          {d.name}
                        </option>
                      ))
                      : null}
                  </select>
              }
            </td>
            <td>
              <select
                className="form-control"
                defaultValue={editableData.ar_age_in_days}
                onChange={e =>
                  this.setState({
                    'ar_age_in_days': e.target.value
                  })
                }
              >
                <option disabled selected>Please select</option>
                {
                  [15, 30, 60, 90].map(d => (
                    <option key={d} value={d}>
                      {d}
                    </option>
                  ))
                }
              </select>
            </td>
            <td>
              <input
                type="number"
                defaultValue={editableData.frequency_in_days}
                className="form-control"
                onInput={e =>
                  this.setState({
                    frequency_in_days: e.target.value
                  })
                }
              />
            </td>
            <td>
              <select
                className="form-control"
                defaultValue={editableData.template_id}
                onChange={e =>
                  this.setState({
                    template_id: e.target.value
                  })
                }
              >
                <option disabled selected>
                  Please select
              </option>
                {emailTemplateList.length
                  ? emailTemplateList.map(d => (
                    <option key={d.id} value={d.id}>
                      {d.name}
                    </option>
                  ))
                  : null}
              </select>
            </td>
            <td>
              <div className="checkbox switcher">
                <label
                  htmlFor={`toggle-${type}-${
                    type === "reccustomer" ? editableData.customer_id : editableData.corporate_entity_id
                    }`}
                >
                  <input
                    type="checkbox"
                    id={`toggle-${type}-${
                      type === "reccustomer" ? editableData.customer_id : editableData.corporate_entity_id
                      }`}
                    defaultChecked={editableData.is_recurring}
                    onChange={e => {
                      e.stopPropagation();
                      this.setState({ is_recurring: !this.state.is_recurring, is_enabled: !this.state.is_recurring, });
                    }}
                  />
                  <span>
                    <small></small>
                  </span>
                </label>
              </div>
            </td>
            <td>
              <button
                title="Cancel"
                className="btn color-blue p-2"
                onClick={e => {
                  e.stopPropagation();
                  this.resetState();
                }}
              >
                <i className="fas fa-remove" />
              </button>
            </td>
          </tr>
        </ClickAwayListener>
      );
    }
  };

  render() {
    const { header, data, type } = this.props;

    const MAX_PAGE_LENGTH = 10;

    const { action, currentPage, editableData, showEmailModal } = this.state;

    let rowData =
      ['company', 'reccompany'].includes(type)
        ? data.filter(d => d.corporate_entity_id !== null)
        : ['customer', 'reccustomer'].includes(type)
        ? data.filter(d => d.customer_id !== null)
        : data;

    if (['reccustomer', 'reccompany'].includes(type)) {
      rowData = rowData.filter(d => d.ar_age_in_days);
    }
    if (['customer', 'company'].includes(type)) {
      rowData = rowData.filter(d => d.ar_age_in_days === null);
    }
    const hidePagination = MAX_PAGE_LENGTH >= rowData.length;
    const totalPages = MAX_PAGE_LENGTH <= rowData.length ? Math.ceil(rowData.length / MAX_PAGE_LENGTH) : 1;
    const disablePrev = currentPage === 1;
    const disableNext = currentPage === totalPages;
    return (
      <div className="clearfix">
        {!editableData ? (
          <button className="btn btn-primary mb-2 mt-3" onClick={() => this.handleAddNewRow(type)}>
            Add New
          </button>
        ) : null}
        <table className={`custom-table ${type === "email" ? "email-table" : ""}`}>
          <thead>
            <tr>
              {header.map(d => (
                <th>{d}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {action === "add" ? ['customer', 'company', 'email'].includes(type) ? this.addNewRow() : this.addNewRecurranceRow() : null}
            {!rowData.length && !action ? (
              <tr>
                <td colSpan={header.length} className="text-center">
                  No Entries
                </td>
              </tr>
            ) : (
                ['customer', 'company', 'email'].includes(type) ? this.renderRows(type, this.paginate(rowData, MAX_PAGE_LENGTH, currentPage)) : this.renderRecurranceTable(type, this.paginate(rowData, MAX_PAGE_LENGTH, currentPage))
            )}
          </tbody>
        </table>
        {!hidePagination ? (
          <div className="pull-right mt-3">
            <button
              onClick={() => this.handlePagination("prev")}
              disabled={disablePrev}
              className="btn btn-primary mr-2 width-94px"
            >
              Previous
            </button>
            <button
              onClick={() => this.handlePagination("next")}
              disabled={disableNext}
              className="btn btn-primary width-94px"
            >
              Next
            </button>
          </div>
        ) : null}
        <EmailTemplate
          defaultData={editableData}
          mode={editableData ? "edit" : "add"}
          show={showEmailModal}
          handleModal={this.handleEmailModal}
          submitEntry={this.submitEmailTemplateEntry}
        />
      </div>
    );
  }
}
