import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from 'react-router-dom';
import cx from 'classnames';
import { instance } from "../../actions/axiosInstance";
import If from 'components/If';
import logo from "assets/images/logo.jpg";
import './style.css';
class ForgotPassword extends Component {
  state = {
    email: {
      value: "",
      isFocused: false,
      error: ""
    },
    formError: ''
  };

  componentDidMount() {
    document.title = "Forgot Password - Payference";
  }

  onSubmit = (e) => {
    e.preventDefault();
    const {
      email,
    } = e.target.elements;

    this.setState({
      email: {
        value: email.value,
        isFocused: false,
        error: !email.value ? 'required' : ''
      },
    }, () => {
      const {
        email,
      } = this.state;
      if (email.value) {
        instance.post('/forgotpassword', {
            email: this.state.email.value,
          }).then((response) => {
            alert(`We emailed a password reset link to ${email.value}. Please follow the instructions in that email.`)
          this.props.history.push('/login');
        }).catch((error) => {
          this.setState({
            formError: 'Invalid Credentials please try again.'
          });
        });
      }
    });
  }

  onChange = (e) => {
    const {
      name,
      value,
    } = e.target;
    this.setState({
      [name]: {
        value,
        isFocused: true,
        error: !value ? 'required' : '',
      }
    });
  }

  onBlurr = (e) => {
    const {
      name,
      value,
    } = e.target;
    this.setState({
      [name]: {
        value,
        isFocused: false,
        error: this.state[name].error,
      }
    });
  }

  togglePassword = () => {
    this.setState({
      showPassword: !this.state.showPassword,
    })
  }

  render() {
    const {
      email,
      formError,
    } = this.state;

    return (
      <div className="loginpage-container">
        <div className="logo">
          <img alt="loading" src={logo} height="60" width="60" />
          <span style={{ color: "white" }}> Payference </span>
        </div>
        <div className="form-widget">
          <h2 className="title">Forgot password</h2>
          <p className="text-center">Please enter your email address, and we will send you a link to reset your password</p>
          <If condition={formError}>
            <span className="form-error">{formError}</span>
          </If>
          <form onSubmit={this.onSubmit}>
            <div className={cx("clearfix field-wrapper", { "label-active": email.isFocused })}>
              <label className={cx("float-left", { focused: email.isFocused })}>
                <span className={cx("label", { focused: email.isFocused })}>Email address</span>
                <input
                  type="text"
                  name="email"
                  placeholder={!email.isFocused ? "Email address" : ""}
                  onFocus={this.onChange}
                  onChange={this.onChange}
                  onBlur={this.onBlurr}
                  autoComplete="off"
                />
              </label>
              <div className="float-right">
                <span className="helptxt">{email.error}</span>
              </div>
            </div>
            <div className="link-wrapper text-center">
              <Link to="/login" className="forgot-password">Back to login</Link>
            </div>
            <button className="button" type="submit">Submit</button>
          </form>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({ login: state });

const mapDispatchToProps = {};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ForgotPassword);
