import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Accordion } from "react-bootstrap";
import { handleAPIError } from "handlers/setters";
import Toaster from "components/Toaster";
import { instance } from "actions/axiosInstance";

function ProfileDetails(props) {
  let company = {
    id: 0,
    name: "Storcentric 2",
    logo: "1",
    industry: "",
    company_type: "",
    address: ""
  };

  const [companyData, setCompanyData] = useState(company);
  const [isProcessing, setIsProcessing] = useState(false);
  const [isPageLoading, setIsPageLoading] = useState(true);

  useEffect(() => {
    function getCompany() {
      let URL = "/client";
      instance
        .get(URL)
        .then(response => {
          if (response.data.result != null && response.data.result[0] != null) {
            const user = {
              id: getValue(response.data.result[0].id),
              name: getValue(response.data.result[0].name),
              department: getValue(response.data.result[0].department),
              role: response.data.result[0].role || "1",
              email: getValue(response.data.result[0].email),
              phone: getValue(response.data.result[0].phone),
              username: getValue(response.data.result[0].username)
            };
            setCompanyData(user);
            setIsPageLoading(false);
          }
        })
        .catch(e => handleAPIError(e));
    }

    getCompany();
  }, []);

  const getValue = e => {
    return e || "";
  };

  const update = e => {
    setIsProcessing(true);
    let URL = "/client";
    // let URL = `/client/user?id=${TokenManager.getUserId()}`;
    instance
      .post(URL, companyData)
      .then(response => {
        setIsProcessing(false);
        Toaster("Profile updated successfully!", "success");
      })
      .catch(e => handleAPIError(e));
  };

  const onChange = e => {
    const { name, value } = e.target;
    setCompanyData(prevState => ({ ...prevState, [name]: value }));
  };

  const onBlurr = e => {
    const { name, value } = e.target;
    setCompanyData(prevState => ({ ...prevState, [name]: value }));
  };

  return (
    <Accordion className="col-md-12" defaultActiveKey="0">
      {/* <Accordion.Toggle className="border mt-4 px-3 cursour-pointer bg-light-grey" as={Card.Header} eventKey="0">
        <h6 className="my-0">Profile Details</h6>
      </Accordion.Toggle> */}
      <Accordion.Collapse className="p-2 px-3" eventKey="0">
        <div className="row">
          <div className="col-md-4">
            <div className="form-group">
              <span>
                <label>Company name</label>
              </span>
              <input type="text" maxLength="50" className="form-control" placeholder="Enter ..." />
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group">
              <div>
                <label>Upload logo</label>
              </div>
              <input type="file" />
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group">
              <span>
                <label>Company type</label>
              </span>
              <select className="form-control">
                <option value>Select</option>
                <option value>Private</option>
                <option value>Public</option>
              </select>
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group">
              <span>
                <label>Industry</label>
              </span>
              <input type="text" maxLength="50" className="form-control" id placeholder="Enter ..." />
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group">
              <span>
                <label>Employees size</label>
              </span>
              <input type="number" className="form-control" placeholder="Enter ..." min={0} />
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group">
              <span>
                <label>Street address</label>
              </span>
              <input type="text" className="form-control" maxLength="200" placeholder="Enter ..." />
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group">
              <span>
                <label>State</label>
              </span>
              <input type="text" maxLength="50" className="form-control" placeholder="Enter ..." />
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group">
              <span>
                <label>City</label>
              </span>
              <input type="text" maxLength="50" className="form-control" placeholder="Enter ..." />
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group">
              <span>
                <label>Country</label>
              </span>
              <input type="text" maxLength="50" className="form-control" placeholder="Enter ..." />
            </div>
          </div>
          <div className="col-md-4">
            <div className="form-group">
              <span>
                <label>Zip code</label>
              </span>
              <input type="text" pattern="[0-9]{5}" className="form-control" placeholder="Enter ..." />
            </div>
          </div>
          <div className="col-md-4" />
          <div className="col-md-4" id="accounts">
            <div className="form-group text-right" style={{ marginTop: "30px" }}>
              <button type="submit" onClick={update} className="btn subbtn btn-success">
                Update
              </button>
            </div>
          </div>
        </div>
      </Accordion.Collapse>
    </Accordion>
  );
}

const mapStateToProps = state => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(ProfileDetails);
