function inputconverter(postdata, allentitites, allbanks) {
  const FIELD_ARRAY = [
    {
      id: "name",
      label: "Name",
      typeid: KEYS.Text,
    },
    {
      id: "department",
      label: "Department",
      typeid: KEYS.Text,
    },
    {
      id: "phone",
      label: "Phone",
      customiserr: ({ fielddata }) => {
        if (fielddata.value && !/^[+]*[(]{0,1}[0-9]{1,4}[)]{0,1}[-\s./0-9]*$/.test(fielddata.value)) {
          return true;
        }
        return false;
      },
      errorText: "Phone number is invalid",
      typeid: KEYS.Text,
    },
    {
      id: "role",
      label: "Role",
      typeid: KEYS.Select,
      disabled: true,
      options: [
        { id: "admin", name: "Admin" },
        { id: "user", name: "User" },
      ],
    },
    {
      id: "email",
      label: "Email",
      typeid: KEYS.Text,
      disabled: true,
      customiserr: ({ fielddata }) => {
        if (!/^\w+([\.-]?\w+)*([+]{0,1}\w+){0,1}@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(fielddata.value)) {
          return true;
        }
        return false;
      },
      errorText: "Email is invalid"
    },
  ];

  return FIELD_ARRAY.map((field) => ({
    ...initjson,
    ...field,
    value: postdata[field.id],
  }));
}

function outputconverter(arr, selectedShift) {
  const result = { ...selectedShift };
  arr.forEach((element) => {
    result[element.id] = element.value;
  });
  return result;
}

function ProfileDetails(props) {
  // let user = {
  //   id: null,
  //   name: "",
  //   department: "",
  //   role: "1",
  //   email: "",
  //   phone: "",
  //   username: ""
  // };

  const [userData, setUserData] = useState(null);
  const [isProcessing, setIsProcessing] = useState(false);
  const [isPageLoading, setIsPageLoading] = useState(true);

  useEffect(() => {
    let URL = `/client/user?id=${TokenManager.getUserId()}`;
    instance
      .get(URL)
      .then((response) => {
        debugger;
        if (response.data.result != null && response.data.result[0] != null) {
          const user = {
            id: getValue(response.data.result[0].id),
            name: getValue(response.data.result[0].name),
            department: getValue(response.data.result[0].department),
            role: get(response, "data.result[0].roles[0]", "") === "payadmin" ? "admin" : (get(response, "data.result[0].roles[0]", "") || "1"),
            email: getValue(response.data.result[0].email),
            phone: getValue(response.data.result[0].phone),
            username: getValue(response.data.result[0].username),
          };
          setUserData(user);
          setIsPageLoading(false);
        }
      })
      .catch((e) => handleAPIError(e));
  }, []);

  function getUser() {
    let URL = `/client/user?id=${TokenManager.getUserId()}`;
    instance
      .get(URL)
      .then((response) => {
        if (response.data.result != null && response.data.result[0] != null) {
          const user = {
            id: getValue(response.data.result[0].id),
            name: getValue(response.data.result[0].name),
            department: getValue(response.data.result[0].department),
            role: response.data.result[0].role || "1",
            email: getValue(response.data.result[0].email),
            phone: getValue(response.data.result[0].phone),
            username: getValue(response.data.result[0].username),
          };
          setUserData(user);
          setIsPageLoading(false);
        }
      })
      .catch((e) => handleAPIError(e));
  }

  const getValue = (e) => {
    return e || "";
  };

  const update = (e) => {
    const { error, fields = [] } = validateMyForm({
      formKey: "editLoggedInUserDetails",
    });
    if (error) {
      return;
    }
    const updatedpostData = { ...userData, ...outputconverter(fields, userData) };
    if (updatedpostData === userData) {
      return;
    }
    setIsProcessing(true);
    let URL = `/client/user?id=${TokenManager.getUserId()}`;
    instance
      .post(URL, updatedpostData)
      .then(() => {
        setIsProcessing(false);
        Toaster("Updated successfully", "success");
      })
      .catch((e) => handleAPIError(e));
  };

  const onChange = (e) => {
    const { name, value } = e.target;
    setUserData((prevState) => ({ ...prevState, [name]: value }));
  };

  const onBlurr = (e) => {
    const { name, value } = e.target;
    setUserData((prevState) => ({ ...prevState, [name]: value }));
  };

  return (
    <Accordion className="col-md-12" defaultActiveKey="0">
      {/* <Accordion.Toggle className="border mt-4 px-3 cursour-pointer bg-light-grey" as={Card.Header} eventKey="0">
        <h6 className="my-0">Graphs</h6>
      </Accordion.Toggle> */}
      <Accordion.Collapse className="p-2 px-3" eventKey="0">
        <div className="row">
          <div className="col-md-12">
            {userData && (
              <DynamicForm fields={inputconverter(userData)} numberOfColumns={3} formKey="editLoggedInUserDetails" />
            )}
          </div>

          <div className="col-md-12">
            <div className="form-group text-right" style={{ marginTop: "30px" }}>
              <button disabled={isPageLoading} type="submit" onClick={update} className="btn subbtn btn-success">
                {isProcessing ? (
                  <span>
                    Updating <i class="fa fa-spinner fa-spin" style={{ fontSize: "11px" }} aria-hidden="true"></i>
                  </span>
                ) : (
                  <span>Update</span>
                )}
              </button>
            </div>
          </div>
        </div>
      </Accordion.Collapse>
    </Accordion>
  );
}

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(ProfileDetails);

