import { ReducersTypes } from "../../constants/ReducersTypes";

export const payables = (
  state = {
    data: "payables data"
  },
  action
) => {
  switch (action.type) {
    case ReducersTypes.PAYABLES:
      return { data: action.payload.data };
    default:
      return state;
  }
};

export default { payables };
