import IconButton from "@material-ui/core/IconButton";
import Snackbar from "@material-ui/core/Snackbar";
import SnackbarContent from "@material-ui/core/SnackbarContent";
// import MuiAlert from "@material-ui/lab/Alert";
import { makeStyles } from "@material-ui/core/styles";
import CloseIcon from "@material-ui/icons/Close";
import store from "store";
import { ReducersTypes } from "constants/ReducersTypes";
import React from "react";
import { connect } from "react-redux";

export const setsnackbarMsg = data => {
  store.dispatch({
    type: ReducersTypes.SNACKBAR_MSG,
    payload: data
  });
};

export const setsnackbarData = data => {
  store.dispatch({
    type: ReducersTypes.SNACKBAR_DATA,
    payload: data
  });
};

export const resetsnackbar = () => {
  store.dispatch({
    type: ReducersTypes.SNACKBAR_RESET
  });
};

// function Alert(props) {
//   return <MuiAlert elevation={6} variant="filled" {...props} />;
// }

const useStyles = makeStyles(theme => ({
  root: {
    width: "100%",
    "& > * + *": {
      marginTop: theme.spacing(2)
    }
  },
  close: {
    padding: theme.spacing(0.5)
  }
}));

function CustomSnackbar(props) {
  const {
    snackbar: { msg = "", variant = "info", onSave }
  } = props;
  const classes = useStyles();
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    resetsnackbar();
  };
  const bgclass = variantBackgrounds[variant];

  return (
    <Snackbar
      anchorOrigin={{ vertical: "top", horizontal: "center" }}
      open={msg ? true : false}
      autoHideDuration={onSave ? 20000 : 2000}
      onClose={handleClose}
    >
      <div className={`snackbar-wrapper ${bgclass}`}>
        <span className="snackbar-msg">{msg}</span>
        <i onClick={handleClose} className="fa fa-times-circle snackbar-closeicon pull-right" aria-hidden="true" />
        {onSave && (
          <i
            onClick={() => {
              handleClose();
              onSave();
            }}
            className="fa fa-check snackbar-closeicon pull-right mr-3"
            aria-hidden="true"
          />
        )}
      </div>
    </Snackbar>
  );
}

const variantBackgrounds = {
  success: "bg-success",
  error: "bg-danger",
  warning: "bg-warning",
  info: "bg-info"
};

const mapStateToProps = state => ({
  snackbar: state.snackbar
});

export default connect(mapStateToProps, null)(CustomSnackbar);
