import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import "./payroll.css";
import { instance } from "actions/axiosInstance";
import {handleAPIError } from "handlers/setters";
import get from "lodash/get";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import { setconfirmationboxData, resetconfirmationboxData } from "components/ConfirmationBox";

const POSTDATA = { ...{ name: null, currency: null } };
export class Entity extends Component {
  state = {
    postdata: null,
    allentities: [],
    line: '',
    isLoading: true,
  };

  componentDidMount() {
    this.getdata();
  }

  updatepostdata = (e) => {
    this.setState({
      postdata: {
        ...this.state.postdata,
        [e.target.name]: e.target.value
      }
    });
  };

  deleteRecord = (id) => {
    setconfirmationboxData({
      variant: "warning",
      msg: "Are you sure you want to delete this Entity?",
      onSave: () => {
        resetconfirmationboxData();
        instance.delete(
          `payroll/delete?id=${id}`
        )
        .then(() => this.getdata())
        .catch(e => handleAPIError(e));
      }
    });
  };

  onSubmit = () => {
    const { postdata: {name, currency, id}, line } = this.state;
    if (!(name && currency)) {
      return;
    }
    const postData = {
      records: [this.state.postdata]
    };
    if (line !== 'new') {
      instance
        .put(`payroll/setting?id=${id}`, postData)
        .then(() => {
          this.setState({ postdata: null, line: '' });
          this.getdata();
        })
        .catch(e => handleAPIError(e));
    } else {
      instance
        .put(`payroll/setting`, postData)
        .then(() => {
          this.setState({ postdata: null, line: '' });
          this.getdata();
        })
        .catch(e => handleAPIError(e));
    }
  };

  getdata = () => {
    instance
      .get("payroll/setting")
      .then(resp => {
        this.setState({ allentities: get(resp, "data.result", []), isLoading: false });
      })
      .catch(e => handleAPIError(e));
  };

  renderEditableRow = (prevData) => {
    return(
      <tr>
        <td>
          <div className="form-group">
            <input
              type="text"
              name="name"
              className="form-control"
              placeholder="Enter ..."
              onChange={e => this.updatepostdata(e)}
              defaultValue={get(prevData, "name", "")}
            />
          </div>
        </td>
        <td>
          <div className="form-group">
            <input
              type="text"
              name="currency"
              className="form-control"
              placeholder="Enter ..."
              onChange={e => this.updatepostdata(e)}
              defaultValue={get(prevData, "currency", "")}
            />
          </div>
        </td>
        <td>
          <div className="form-group">
            <div className="btn payrollsumbtn cursor-pointer" onClick={this.onSubmit}>
              <i className="fa fa-save color-blue" />
            </div>
            <div
              onClick={() => this.setState({ postdata: null, line: '' })}
              className="btn mr-3 cursor-pointer"
            >
              <i className="fa fa-remove color-blue" />
            </div>
          </div>
        </td>
      </tr>
    );
  };

  render() {
    const { postdata, line, allentities = [], isLoading } = this.state;
    return (
      <div className="row px-15" id="dashboardBody">
        <div className="col-md-12">
          <div className="card">
            <div className="card-body payrollmanage">
              <div className="row">
                <div className="col-md-12">
                  <form action="" method="post">
                    <div className="row">
                      <div className="col-md-12">
                        <div style={{ clear: "both" }}>
                          <div
                            className="titletranshist"
                            style={{ fontSize: "18px", paddingBottom: "15px", float: "left" }}
                          >
                            <Link to="/settings" key="back">
                              <i className="fa fa-arrow-left" style={{ color: "#ddd", marginRight: "5px" }} />{" "}
                            </Link>
                            &nbsp;Payroll - Entity
                          </div>
                          {/* <div className="dropdown float-right">
                            <a
                              className="btn dropdown-toggle btn-primary"
                              href="#"
                              role="button"
                              id="export"
                              data-toggle="dropdown"
                              aria-haspopup="true"
                              aria-expanded="false"
                            >
                              Export
                            </a>
                            <div
                              className="dropdown-menu dropdown-menu-right"
                              aria-labelledby="dropdownMenuLink"
                              id="exportlist"
                            >
                              <a href="#">Export CSV</a>
                              <a href="#">Export Excel</a>
                            </div>
                          </div> */}
                        </div>
                      </div>
                      <ClickAwayListener onClickAway={() => line === 'new' ? this.setState({ postData: null, line: '' }) : this.onSubmit()}>
                        <div className="col-md-12">
                          <div className="titletranshist" style={{ fontSize: 18, marginBottom: 15 }}>
                            {!line && (
                              <React.Fragment>
                                <span>Entity</span>
                                <i
                                  onClick={e => {
                                    e.stopPropagation();
                                    this.setState({ postdata: POSTDATA, line: 'new' });
                                  }}
                                  className="fa fa-plus cashinflowclick"
                                  title="Click to add item"
                                  aria-hidden="true"
                                />
                              </React.Fragment>
                            )}
                          </div>
                          <div className="">
                            <div className="titletranshist" style={{ fontSize: 18, marginBottom: 15 }}>
                              List of entity
                            </div>
                            <form action="" method="post">
                              <div className="payrolltable">
                                <table>
                                  <tbody>
                                    <tr>
                                      <th>Entity</th>
                                      <th>Currency</th>
                                      <th>Action</th>
                                    </tr>
                                    {
                                      isLoading ? <tr><td colspan="3" className="text-center">Loading..</td></tr> : null
                                    }
                                    {
                                      line === 'new' ? this.renderEditableRow() : null
                                    }
                                    {
                                      allentities &&
                                      allentities.map((entity, d) => {
                                        if(line === d) {
                                          return this.renderEditableRow(postdata)
                                        }
                                        return(
                                          <tr className="cursor-pointer" title="Edit" onClick={() => { this.setState({ postdata: entity, line: d }) }}>
                                            <td>{entity.name}</td>
                                            <td>{entity.currency}</td>
                                            <td>{/* <i onClick={e => {e.stopPropagation(); this.deleteRecord(entity.id)}} className="fa fa-trash cursor-pointer color-blue" title="Delete" /> */}</td>
                                          </tr>
                                        )
                                      })
                                    }
                                  </tbody>
                                </table>
                              </div>
                            </form>
                          </div>
                        </div>
                      </ClickAwayListener>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(Entity);
