import React, { Component } from "react";
import cx from "classnames";
import get from "lodash/get";
import { instance } from "../../actions/axiosInstance";
import If from "components/If";
import logo from "assets/images/logo.jpg";
import "../forgetPassword/style.css";
import TokenManager from "utils/TokenManager";
import { getParameterByName } from "utils";
import { passwordRegEx } from "constants/regex";

class ResetPassword extends Component {
  state = {
    password: {
      value: "",
      isFocused: false,
      error: ""
    },
    confirmPassword: {
      value: "",
      isFocused: false,
      error: ""
    },
    showPassword: false,
    showConfirmPassword: false,
    formError: "",
    urlToken: getParameterByName("token")
  };

  componentDidMount() {
    document.title = "Reset Password - Payference";
    if (this.state.urlToken) {
      instance
        .get("/token", {
          auth: {
            username: this.state.urlToken,
            password: Math.random().toString(20)
          }
        })
        .then(response => {
          TokenManager.set(response.data.token, response.data["expires at"]);
          TokenManager.setUserName(response.data.username, response.data["id"]);

          let URL = `/client/user?id=${response.data.id}`;
          instance.get(URL).then(resp => {
            const datatemp = {
              ...response.data,
              ...get(resp, "data.result[0]", {})
            };
            TokenManager.setUserData(datatemp);

            if (!response.data.status) {
              this.props.history.push("/reset-password");
            } else {
              // window.location.reload();
            }
          });
        });
    }
  }

  onSubmit = e => {
    e.preventDefault();
    const { password, confirmPassword } = e.target.elements;

    this.setState(
      {
        password: {
          value: password.value,
          isFocused: false,
          error: !password.value ? "required" : this.state.password.error
        },
        confirmPassword: {
          value: confirmPassword.value,
          isFocused: false,
          error: !confirmPassword.value ? "required" : this.state.password.error
        }
      },
      () => {
        const { password, confirmPassword } = this.state;
        if (password.value && confirmPassword.value && !password.error && !confirmPassword.error) {
          instance
            .post(
              "/user/setpassword",
              {
                password: this.state.password.value
              },
              {
                auth: {
                  username: TokenManager.get(),
                  password: this.state.password.value
                }
              }
            )
            .then(() => {
              alert("Password reset successful.");
              this.props.history.push("/dashboard");
            })
            .catch(error => {
              this.setState({
                formError: "Something went wrong please try again."
              });
            });
        }
      }
    );
  };

  onChange = e => {
    const { name, value } = e.target;
    this.setState({
      [name]: {
        value,
        isFocused: true,
        error: ""
      }
    });
  };

  onBlurr = e => {
    const { name, value } = e.target;
    this.setState({
      [name]: {
        value,
        isFocused: false,
        error: value
          ? name === "password"
            ? !passwordRegEx.test(value)
              ? "8 characters, minimum 1 digit"
              : ""
            : name === "confirmPassword"
            ? this.state.password.value !== value
              ? "Not matching"
              : ""
            : ""
          : "required"
      }
    });
  };

  chechPasswordEquality = () => {
    const { password, confirmPassword } = this.state;
    if (!confirmPassword.value) {
      return "";
    }
    if (password.value !== confirmPassword.value) {
      return "Password does't match";
    }
    return "";
  };

  togglePassword = () => {
    this.setState({
      showPassword: !this.state.showPassword
    });
  };

  checkPasswordStrength = password => {
    return "";
    // return password ? "8 characters, minimum 1 digit" : "";
    // if(password.length !== 8) {
    //   return 'Password should contain minimum 8 characters';
    // }
    // if (!(/[a-zA-Z]/.test(password))) {
    //   return 'Password should contain atleast 1 character';
    // }
    // if (!(/\d/.test(password))) {
    //   return 'Password should contain atleast 1 digit';
    // }
  };

  render() {
    const { password, formError, urlToken, confirmPassword, showConfirmPassword, showPassword } = this.state;

    return (
      <div className="loginpage-container">
        <div className="logo">
          <img alt="loading" src={logo} height="60" width="60" />
          <span style={{ color: "white" }}> Payference </span>
        </div>
        <div className="form-widget">
          <h2 className="title">Enter Password</h2>
          {urlToken ? null : (
            <p className="text-center">
              Welcome to Payference! please select a password to get started
            </p>
          )}
          <If condition={formError}>
            <span className="form-error">{formError}</span>
          </If>
          <form onSubmit={this.onSubmit}>
            <div
              className={cx("clearfix field-wrapper", {
                "label-active": password.isFocused,
                "border border-danger": password.error && !password.isFocused
              })}
            >
              <label className={cx("float-left", { focused: password.isFocused })}>
                <span className={cx("label", { focused: password.isFocused })}>Password</span>
                <input
                  type={showPassword ? "text" : "password"}
                  name="password"
                  placeholder={!password.isFocused ? "8 characters, minimum 1 digit" : ""}
                  onFocus={this.onChange}
                  onChange={this.onChange}
                  onBlur={this.onBlurr}
                  autoComplete="off"
                />
              </label>
              <div className="float-right">
                <span className="helptxt">{password.error}</span>
                {/* <span
                  className="show-hide-toggle"
                  role="presentation"
                  onClick={() => this.setState({ showPassword: !showPassword })}
                >
                  {showPassword ? (
                    <i className="fa fa-eye" style={{ cursor: "pointer", fontSize: "15px" }} title="Show Password" />
                  ) : (
                    <i
                      className="fa fa-eye-slash"
                      style={{ cursor: "pointer", fontSize: "15px" }}
                      title="Show Password"
                    />
                  )}
                </span> */}
              </div>
            </div>
            <div
              className={cx("clearfix field-wrapper", {
                "label-active": confirmPassword.isFocused,
                "border border-danger": confirmPassword.error && !confirmPassword.isFocused
              })}
            >
              <label className={cx("float-left", { focused: confirmPassword.isFocused })}>
                <span className={cx("label", { focused: confirmPassword.isFocused })}>Confirm Password</span>
                <input
                  type={showConfirmPassword ? "text" : "password"}
                  name="confirmPassword"
                  placeholder={!confirmPassword.isFocused ? "Confirm Password" : ""}
                  onFocus={this.onChange}
                  onChange={this.onChange}
                  onBlur={this.onBlurr}
                  autoComplete="off"
                />
              </label>
              <div className="float-right">
                <span className="helptxt">{confirmPassword.error}</span>
                <span
                  className="show-hide-toggle"
                  role="presentation"
                  onClick={() => this.setState({ showConfirmPassword: !showConfirmPassword })}
                >
                  {showConfirmPassword ? (
                    <i className="fa fa-eye" style={{ cursor: "pointer", fontSize: "15px" }} title="Show Password" />
                  ) : (
                    <i
                      className="fa fa-eye-slash"
                      style={{ cursor: "pointer", fontSize: "15px" }}
                      title="Show Password"
                    />
                  )}
                </span>
              </div>
            </div>
            <button
              className="btn btn-primary w-100 p-3 mt-2"
              type="submit"
              disabled={!confirmPassword.value || confirmPassword.error || password.error }
            >
              Submit
            </button>
          </form>
        </div>
      </div>
    );
  }
}

export default ResetPassword;
