import React from "react";
import { Link } from "react-router-dom";
import { CSVLink } from "react-csv";
import * as XLSX from "xlsx";
import { instance } from "../../../actions/axiosInstance";
import "./receivables.css";
import moment from "moment";
import * as FileSaver from "file-saver";
import ClickAwayListener from "components/ClickAwayListener";
import get from "lodash/get";
import { Accordion, Card } from "react-bootstrap";
import Toaster from "components/Toaster";
import CustomTable from "./CustomTable";
import fe_config from "constants/Configs";
import debounce from "lodash/debounce";
import { handleAPIError } from "handlers/setters";
import { setconfirmationboxData, resetconfirmationboxData } from "components/ConfirmationBox";

export const ExportXLSX = csvData => {
  const filename = `payeables${moment().format("MMMM Do YYYY-h:mm:ss-a")}.xlsx`;
  const fileType = "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8";
  const ws = XLSX.utils.json_to_sheet(csvData);
  const wb = { Sheets: { data: ws }, SheetNames: ["data"] };
  const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" });
  const data = new Blob([excelBuffer], { type: fileType });
  FileSaver.saveAs(data, filename);
};

function ExportCSV(data) {
  const csvData = data.map(element => ({
    Supplier: get(element, "supplier.name", ""),
    InvoiceNo: get(element, "invoice_number", ""),
    DueDate: moment(get(element, "due_date", "")).format("YYYY/MM/DD"),
    Recommendation: get(element, "recommendation", ""),
    InvoiceDate: moment(get(element, "invoice_date", "")).format("YYYY/MM/DD"),
    AmountDue: get(element, "total_amount_due", "")
  }));

  return csvData;
}

class PayableSettings extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showexport: false,
      emailTemplateData: [],
      remindersData: [],
      companyListing: [],
      customerListing: [],
      customerListNames: [],
      globalReminder: false,
      isCustomerListLoading: false,
      customerCategoryList: [],
      line: '',
      currPage: 1,
      selectedData: null,
      isCustCategoryLoading: true,
    };
    this.debounceMethod = debounce(this.searchCustomer.bind(this), 200);
  }

  componentDidMount() {
    this.getCompanyLevelReminder();
    this.getEmailTemplates();
    this.getCustomerNames();
    this.getCompanyNames();
    this.getGlobalReminder();
    this.getCustomerCategory();
    const hash = this.props.location.hash;
    if (hash) {
      document.getElementById(hash.replace('#', '')).scrollIntoView(true);
    }
  }

  componentDidUpdate() {
    const inputNumbers = document.querySelectorAll("input[type=number]");
    if (inputNumbers.length) {
      var invalidChars = ["-", "+", "e"];
      for (let i = 0; i < inputNumbers.length; i++) {
        inputNumbers[i].addEventListener("input", function() {
          this.value = this.value.replace(/[e\+\-]/gi, "");
        });

        inputNumbers[i].addEventListener("keydown", function(e) {
          if (invalidChars.includes(e.key)) {
            e.preventDefault();
          }
        });
      }
    }
  }

  reloadListing = type => {
    if (['reccustomer', 'reccompany', 'company', 'customer'].includes(type)) {
      this.getCompanyLevelReminder();
    } else {
      this.getEmailTemplates();
    }
  };

  getGlobalReminder = () => {
    const URL = "client";
    instance
      .get(URL)
      .then(response => {
        this.setState({
          globalReminder: response.data.result.receivable_rem_enabled
        });
      })
      .catch(e => handleAPIError(e));
  };

  getCustomerCategory = () => {
    this.setState({
      isCustCategoryLoading: true,
    });
    instance
      .get(`customercategory?per_page=100`)
      .then(response => {
        this.setState({
          customerCategoryList: response.data.result.sort((a, b) => (+b.is_selected - +a.is_selected)),
          isCustCategoryLoading: false,
        })
      })
      .catch(e => handleAPIError(e));
  }

  onSubmit = () => {
    const { selectedData } = this.state;
    const URL = selectedData.id ? `customercategory?id=${selectedData.id}` : 'customercategory';
    const method = selectedData.id ? 'patch' : 'post';
    const requestObject = {
      name: selectedData.name,
      internal_id: Number(selectedData.internal_id),
      is_selected: selectedData.is_selected
    };

    if(!selectedData.internal_id) {
      delete requestObject.internal_id;
    }

    instance
    [method](URL, { ...requestObject})
      .then(() => {
        this.setState({
          line: '',
          selectedData: null,
        });
        this.getCustomerCategory();
        localStorage.setItem("refresh_reports", true);
      })
      .catch(e => handleAPIError(e));
  };

  onDelete = (id) => {
    instance
      .delete(`customercategory?id=${id}`)
      .then(() => {
        this.getCustomerCategory();
        localStorage.setItem("refresh_reports", true);
      })
      .catch(e => handleAPIError(e));
  }
  renderEditableRow = () => {
    const {
      selectedData: {
        id,
        name,
        internal_id,
        is_selected,
      },
      customerCategoryList
    } = this.state;
    const isExistingId = !id && customerCategoryList.some(d => d.internal_id === Number(internal_id));
    return (
      <tr>
        <td className="text-sm-center">
          <input type="checkbox" className="md-checkbox"  defaultChecked={is_selected} onChange={e => this.setState({ selectedData: { ...this.state.selectedData, is_selected: +e.target.checked } })} />
        </td>
        <td>
          <input
            type="text"
            onChange={e => this.setState({ selectedData: { ...this.state.selectedData, name: e.target.value, is_selected: +e.target.value } })}
            className="form-control"
            defaultValue={name}
          />
        </td>
        <td>
          <input
            type="number"
            onInput={e => this.setState({ selectedData: { ...this.state.selectedData, internal_id: e.target.value } })}
            readOnly={!!id}
            className={isExistingId ? "error form-control" : "form-control"}
            defaultValue={internal_id}
          />
        </td>
        <td className="text-sm-center">
          <button disabled={!name || isExistingId} className="btn" onClick={this.onSubmit} >
            <i className="fa fa-save cursor-pointer color-blue" />
          </button>
          {"|"}
          <button className="btn" onClick={() => this.setState({ line: '', selectedData: null })}>
            <i className="fa fa-remove cusor-pointer color-blue" />
          </button>
        </td>
      </tr>
    )
  }

  getCustomerNames = () => {
    const URL = "customer?per_page=4000";
    instance
      .get(URL)
      .then(response => {
        this.setState({
          customerListNames: response.data.result
        });
      })
      .catch(e => handleAPIError(e));
  };

  getCompanyNames = () => {
    const URL = "payroll/setting?per_page=100";
    instance
      .get(URL)
      .then(response => {
        this.setState({
          companyListing: response.data.result
        });
      })
      .catch(e => handleAPIError(e));
  };

  getCompanyLevelReminder = () => {
    const URL = "receivable-reminder";
    instance
      .get(URL)
      .then(response => {
        this.setState({
          remindersData: get(response, "data.result", []).reverse()
        });
      })
      .catch(e => handleAPIError(e));
  };

  getEmailTemplates = () => {
    const URL = "receivable-reminder-email-template";
    instance
      .get(URL)
      .then(response => {
        this.setState({
          emailTemplateData: get(response, "data.result", []).reverse()
        });
      })
      .catch(e => handleAPIError(e));
  };

  setshowExport = flag => {
    this.setState({ showexport: flag });
  };

  toggleGlobalAutoReminder = flag => {
    const URL = "/client";
    instance
      .post(
        URL,
        {
          receivable_rem_enabled: flag
        }
      )
      .then(() => {
        Toaster("Updated Reminder", "success");
      })
      .catch(e => handleAPIError(e));
  };

  searchCustomer = (text) => {
    if (text) {
      this.setState({
        isCustomerListLoading: true,
      })
      instance
        .get(`arinvoice/search?customer=${text}&per_page=100`)
        .then(res => {
          this.setState({
            customerListing: res.data.result,
            isCustomerListLoading: false,
          });
        })
        .catch(e => handleAPIError(e));
    }
  };

  handlePagination = (action) => {
    const {
      currPage,
    } = this.state;
    let newPage = currPage;
    if (action === 'prev') {
      newPage = currPage === 1 ? 1 : currPage - 1;
    }
    if (action === 'next') {
      newPage = currPage + 1;
    }
    this.setState(
      {
        currPage: newPage
      }
    );
  }

  paginate = (array, page_size, page_number) => array.slice((page_number - 1) * page_size, page_number * page_size);

  render() {
    const {
      line,
      currPage,
      showexport,
      remindersData,
      globalReminder,
      companyListing,
      customerListing,
      customerListNames,
      isCustomerListLoading,
      emailTemplateData,
      customerCategoryList,
      isCustCategoryLoading,
    } = this.state;
    const customerHeader = ["Customer", "Type", "Days", "Email Template", "Combine Reminders", "Status", "Action"];
    const companyHeader = ["Entity", "Type", "Days", "Email Template", "Combine Reminders", "Status", "Action"];
    const emailTableHeader = ["Id", "Name", "Subject", "Body", "Action"];
    const recurringTableHeader = ["Customer", "AR Age", "Frequency", "Email Template", "Status", "Action"];
    const disablePrev = currPage === 1;
    const disableNext = !customerCategoryList.length || customerCategoryList.length < 20;
    const expand = this.props.location.hash;

    return (
      <div className="row" id="dashboardBody">
        <div className="col-md-12">
          <div className="card">
            <div className="card-body">
              <div className="row">
                <div className="col-md-12">
                  <div className="paynewdetsec">
                    <div className="float-left" style={{ fontSize: "18px", paddingBottom: "15px", float: "left" }}>
                      <Link to="/settings" title="Back">
                        <i
                          className="fa fa-arrow-left"
                          aria-hidden="true"
                          style={{ color: "#ddd", marginRight: "10px" }}
                        ></i>
                      </Link>
                      Receivables Settings
                    </div>
                    <div className="dropdown float-right">
                      {fe_config.MODE === "DEV" &&<span
                        className="btn dropdown-toggle btn-primary"
                        role="button"
                        id=""
                        data-toggle="dropdown"
                        aria-haspopup="true"
                        aria-expanded="false"
                        onClick={() => this.setshowExport(true)}
                      >
                        Export
                      </span>}
                      <ClickAwayListener onClickAway={this.setshowExport}>
                        <div
                          className={`dropdown-menu dropdown-menu-right payexpdroplist ${showexport && "show"}`}
                          aria-labelledby="dropdownMenuLink"
                          id=""
                        >
                          <CSVLink
                            data={ExportCSV([])}
                            filename={`payable_${moment().format("MMMM Do YYYY-h:mm:ss-a")}.csv`}
                          >
                            Export CSV
                          </CSVLink>
                          <span
                            style={{ fontSize: 12, float: "right", marginRight: "10px", cursor: "pointer" }}
                            onClick={() => ExportXLSX(ExportCSV([]))}
                          >
                            Export Excel
                          </span>
                        </div>
                      </ClickAwayListener>
                    </div>
                  </div>
                </div>

                {fe_config.MODE === "DEV" && (
                  <div className="col-md-12">
                    <div className="divider" />
                    <div style={{ fontSize: "18px", marginBottom: "15px" }}>Preferences</div>
                    <div className="row">
                      <div className="col-md-4">
                        <div className="form-group">
                          <label>Single payment for multiple invoices</label>
                          <select
                            className="form-control"
                            onChange={e => this.setState({ invoiceOption: e.target.value })}
                          >
                            <option value="">Select</option>
                            <option value="1">Yes</option>
                            <option value="2">No</option>
                          </select>
                        </div>
                      </div>
                    </div>
                  </div>
                )}
                {fe_config.MODE === "DEV" && (
                  <div className="col-md-12">
                    <div className="divider" />
                    <div style={{ fontSize: "18px", marginBottom: "15px" }}>Templates</div>
                    <div className="row">
                      <div className="col-md-4">
                        <div className="form-group">
                          <label>Single payment for multiple invoices</label>
                          <select
                            className="form-control"
                            onChange={e => this.setState({ invoiceOption: e.target.value })}
                          >
                            <option value="">Select</option>
                            <option value="1">Yes</option>
                            <option value="2">No</option>
                          </select>
                        </div>
                      </div>
                    </div>
                  </div>
                )}
                {fe_config.MODE === "DEV" && (
                  <div className="col-md-12">
                    <div className="divider" />
                    <div className="titletranshist" style={{ fontSize: "18px", marginBottom: "15px" }}>
                      Payments Set-up
                    </div>
                    <form action="" method="post">
                      <div className="row">
                        <div className="col-md-4">
                          <div className="form-group">
                            <span>
                              <label>Spend category</label>
                            </span>
                            <select className="form-control">
                              <option value="">Select</option>
                              <option value="">All</option>
                              <option value="">Office supplies</option>
                              <option value="">Payoll</option>
                            </select>
                          </div>
                        </div>
                        <div className="col-md-4">
                          <div className="form-group">
                            <span>
                              <label>Spend amount</label>
                            </span>
                            <select className="form-control">
                              <option value="">Select</option>
                              <option value="">$2M/month</option>
                              <option value="">$25K/month</option>
                              <option value="">$25M/month</option>
                            </select>
                          </div>
                        </div>
                        <div className="col-md-4">
                          <div className="form-group">
                            <span>
                              <label>Payment method</label>
                            </span>
                            <select className="form-control">
                              <option value="">Select</option>
                              <option value="">Credit - 5678</option>
                              <option value="">P card - 2345</option>
                              <option value="">ACH - 2384</option>
                            </select>
                          </div>
                        </div>
                        <div className="col-md-4"></div>
                        <div className="col-md-4"></div>
                        <div className="col-md-4">
                          <div className="form-group text-right" style={{ marginTop: "30px" }}>
                            <button type="submit" className="btn subbtn btn-success">
                              Update
                            </button>
                          </div>
                        </div>
                      </div>
                    </form>
                  </div>
                )}
                <React.Fragment>
                  <div className="divider" />
                  <div className="col-md-12 mt-4">
                    <div className="row">
                      <h6 className="mr-2 col-md-2">Auto Reminders</h6>
                      <div className="checkbox switcher" key={globalReminder}>
                        <label htmlFor="test">
                          <input
                            type="checkbox"
                            defaultChecked={globalReminder}
                            id="test"
                            onChange={e => this.toggleGlobalAutoReminder(e.target.checked)}
                          />
                          <span>
                            <small></small>
                          </span>
                        </label>
                      </div>
                    </div>
                  </div>
                  <div className="divider" />
                  <Accordion className="col-md-12" defaultActiveKey={expand === '#companyreminders' && "0"} id="companyreminders">
                    <Accordion.Toggle
                      className="border mt-4 px-3 cursour-pointer bg-light-grey"
                      as={Card.Header}
                      eventKey="0"
                    >
                      <h6 className="my-0">Company Level Reminders</h6>
                    </Accordion.Toggle>
                    <Accordion.Collapse className="py-2" eventKey="0">
                      <div className="col-md-9">
                        <CustomTable
                          type="company"
                          data={remindersData}
                          header={companyHeader}
                          companyList={companyListing}
                          reloadListing={this.reloadListing}
                          emailTemplateList={emailTemplateData}
                        />
                        <div className="divider mt-3 mb-3 p-0" />
                        <h6><strong>Recurring Reminders:</strong></h6>
                        <CustomTable
                          type="reccompany"
                          data={remindersData}
                          header={['Entity', ...recurringTableHeader.slice(1, 6)]}
                          companyList={companyListing}
                          reloadListing={this.reloadListing}
                          emailTemplateList={emailTemplateData}
                        />
                      </div>
                    </Accordion.Collapse>
                  </Accordion>
                  <Accordion className="col-md-12" defaultActiveKey={expand === '#customerreminders' && "0"} id="customerreminders">
                    <Accordion.Toggle
                      className="border mt-4 px-3 cursour-pointer bg-light-grey"
                      as={Card.Header}
                      eventKey="0"
                    >
                      <h6 className="my-0">Customer Level Reminders</h6>
                    </Accordion.Toggle>
                    <Accordion.Collapse className="py-2" eventKey="0">
                      <div className="col-md-9">
                        <CustomTable
                          type="customer"
                          data={remindersData}
                          header={customerHeader}
                          customerList={customerListing}
                          reloadListing={this.reloadListing}
                          emailTemplateList={emailTemplateData}
                          searchCustomer={this.searchCustomer}
                          isCustomerListLoading={isCustomerListLoading}
                          customerListNames={customerListNames}
                        />
                        <div className="divider mt-3 mb-3 p-0" />
                        <h6><strong>Recurring Reminders:</strong></h6>
                        <CustomTable
                          type="reccustomer"
                          data={remindersData}
                          header={recurringTableHeader}
                          customerList={customerListing}
                          reloadListing={this.reloadListing}
                          emailTemplateList={emailTemplateData}
                          searchCustomer={this.searchCustomer}
                          isCustomerListLoading={isCustomerListLoading}
                          customerListNames={customerListNames}
                        />
                      </div>
                    </Accordion.Collapse>
                  </Accordion>
                  <Accordion className="col-md-12" defaultActiveKey={expand === '#emailtemplates' && "0"} id="emailtemplates">
                    <Accordion.Toggle
                      className="border mt-4 px-3 cursour-pointer bg-light-grey"
                      as={Card.Header}
                      eventKey="0"
                    >
                      <h6 className="my-0">Templates</h6>
                    </Accordion.Toggle>
                    <Accordion.Collapse className="py-2" eventKey="0">
                      <div className="col-md-9">
                        <CustomTable
                          type="email"
                          data={emailTemplateData}
                          header={emailTableHeader}
                          reloadListing={this.reloadListing}
                        />
                      </div>
                    </Accordion.Collapse>
                  </Accordion>
                </React.Fragment>
                <Accordion className="col-md-12" defaultActiveKey={expand === '#customercategory' && "0"} id="customercategory">
                  <Accordion.Toggle
                    className="border mt-4 px-3 cursour-pointer bg-light-grey"
                    as={Card.Header}
                    eventKey="0"
                  >
                    <h6 className="my-0">Customer Category</h6>
                  </Accordion.Toggle>
                  <Accordion.Collapse className="py-2" eventKey="0">
                    <div className="col-md-12">
                      <ClickAwayListener onClickAway={() => this.setState({ line: '', selectedData: null })}>
                        <div className="paynewdetsec">
                          {
                            (line !== 'new') ?
                              <button className="btn btn-primary mb-2 mt-3" onClick={e => {
                                e.stopPropagation();
                                this.setState({ selectedData: { name: '', internal_id: '' }, line: 'new' });
                              }}>Add New</button> : null
                          }
                          <div className="table-container">
                            <table className="paynewdetsectran">
                              <tr>
                                <th className="column1 text-sm-center">Reports</th>
                                <th>Name</th>
                                <th>Internal Id</th>
                                <th className="text-sm-center">Action</th>
                              </tr>
                              {
                                line === 'new' ? this.renderEditableRow() : null
                              }
                              {
                                this.paginate(customerCategoryList, 20, currPage).map((d, index) => {
                                  if (line === index) {
                                    return this.renderEditableRow();
                                  } else {
                                    return (
                                      <tr onClick={() => this.setState({ line: index, selectedData: d, })} key={d.name}>
                                        <td className="text-sm-center">
                                          <input type="checkbox" className="md-checkbox" onClick={e => e.stopPropagation()} name={d.id} defaultChecked={d.is_selected} onChange={e => { e.stopPropagation(); this.setState({ selectedData: { ...d, is_selected: +e.target.checked } }, this.onSubmit) }} />
                                        </td>
                                        <td>
                                          {d.name}
                                        </td>
                                        <td>
                                          {d.internal_id}
                                        </td>
                                        <td className="text-sm-center">
                                          <button
                                            className="btn"
                                            onClick={e => {
                                              e.stopPropagation();

                                              setconfirmationboxData({
                                                variant: "warning",
                                                msg: "Are you sure you want to delete this category?",
                                                onSave: () => {
                                                  resetconfirmationboxData();
                                                  this.onDelete(d.id);
                                                }
                                              });
                                            }}
                                          >
                                            <i className="fa fa-trash-o cursor-pointer color-blue" />
                                          </button>
                                        </td>
                                      </tr>
                                    )
                                  }
                                })
                              }
                            </table>
                            {
                              isCustCategoryLoading ? <div className="loader">Loading...</div> : null
                            }
                          </div>
                          <div className="pull-right mt-3">
                            <button
                              onClick={() => this.handlePagination("prev")}
                              className="btn btn-primary mr-2 width-94px"
                              disabled={disablePrev}
                            >
                              Previous
                            </button>
                            <button
                              onClick={() => this.handlePagination("next")}
                              className="btn btn-primary width-94px"
                              disabled={disableNext}
                            >
                              Next
                            </button>
                          </div>
                        </div>
                      </ClickAwayListener>
                    </div>
                  </Accordion.Collapse>
                </Accordion>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default PayableSettings;
