import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { instance } from "actions/axiosInstance";
import { handleAPIError } from "handlers/setters";
import Toaster from "components/Toaster";
import TokenManager from "utils/TokenManager";
import './style.css';

const Integrations = (props) => {
    const [apQueryEP, setApQueryEp] = useState('');
    const [arQueryEP, setArQueryEp] = useState('');
    const [obQueryEP, setObQueryEp] = useState('');
    const [erp, setERP] = useState('');
    const [netSuite, setNetSuiteConfig] = useState({
        account: '',
        netSuiteKey: '',
        consumerSecret: '',
        tokenKey: '',
        tokenSecret: '',
    });

    useEffect(() => {
        if (!TokenManager.getIsAdmin()) {
            props.history.push('/dashboard');
            return;
        }
        getIntegration();
    }, []);

    const getIntegration = () => {
        instance.get('integration/configuration')
        .then(response => {
            const resp = response.data.result;
            if(resp) {
                setApQueryEp(resp.erp_endpoint_ap);
                setArQueryEp(resp.erp_endpoint_ar);
                setObQueryEp(resp.erp_endpoint_ob);
                setERP(resp.erp_type);
                setNetSuiteConfig({
                    account: resp.ns_account,
                    netSuiteKey: resp.ns_consumer_key,
                    consumerSecret: resp.ns_consumer_secret,
                    tokenKey: resp.ns_token_key,
                    tokenSecret: resp.ns_token_secret,
                });
            }
        })
    };

    const submitIntegration = () => {
        const requestObject = {
            "erp_endpoint_ap": apQueryEP,
            "erp_endpoint_ar": arQueryEP,
            "erp_endpoint_ob": obQueryEP,
            "erp_type": erp,
            "ns_account": netSuite.account,
            "ns_consumer_key": netSuite.netSuiteKey,
            "ns_consumer_secret": netSuite.consumerSecret,
            "ns_token_key": netSuite.tokenKey,
            "ns_token_secret": netSuite.tokenSecret,
        };
        instance
            .post('integration/configuration', { ...requestObject })
            .then(() => {
                getIntegration();
                Toaster("Data integration updated successfully!", "success");
            }).catch(e => handleAPIError(e));
    };

    const disabledSubmit = () => (!apQueryEP || !arQueryEP || !obQueryEP || !erp || !netSuite.account || !netSuite.netSuiteKey || !netSuite.consumerSecret || !netSuite.tokenKey || !netSuite.tokenSecret);

    const encodeValues = (value) => (value && 'X'.repeat(10) + value.substr(value.length - 4));

    return (
        <div className="row integration-form">
            <div className="col-md-12">
                <div className="row">
                    <div className="col-md-12">
                        <div className="form-group">
                            <label>Payables query Endpoint</label>
                            <input type="text" className="form-control" defaultValue={encodeValues(apQueryEP)} onChange={e => setApQueryEp(e.target.value)} placeholder="Payables query Endpoint" />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-12">
                        <div className="form-group">
                            <label>Receivables query Endpoint</label>
                            <input type="text" className="form-control" defaultValue={encodeValues(arQueryEP)} onChange={e => setArQueryEp(e.target.value)} placeholder="Receivables query Endpoint" />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-12">
                        <div className="form-group">
                            <label>Order Backlog query Endpoint</label>
                            <input type="text" className="form-control" defaultValue={encodeValues(obQueryEP)} onChange={e => setObQueryEp(e.target.value)} placeholder="Order Backlog query Endpoint" />
                        </div>
                    </div>
                </div>
            </div>
            <div className="col-md-12">
                <div className="divider" />
                <div className="row">
                    <div className="col-md-4">
                        <div className="form-group">
                            <label>ERP</label>
                            <select className="form-control" defaultValue={erp} onChange={e => setERP(e.target.value)}>
                                <option disabled selected>Select ERP</option>
                                <option value="netsuite">Netsuite</option>
                            </select>
                        </div>
                    </div>
                </div>
                {
                    erp === 'NETSUITE' ?
                        <React.Fragment>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label>Netsuite Account</label>
                                        <input type="text" className="form-control" defaultValue={encodeValues(netSuite.account)} onChange={e => setNetSuiteConfig({ ...netSuite, account: e.target.value})} placeholder="Netsuite Account" />
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label>Netsuite Consumer Key</label>
                                        <input type="text" className="form-control" defaultValue={encodeValues(netSuite.netSuiteKey)} onChange={e => setNetSuiteConfig({ ...netSuite, netSuiteKey: e.target.value })} placeholder="Netsuite Consumer Key" />
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label>Netsuite Consumer Secret</label>
                                        <input type="text" className="form-control" defaultValue={encodeValues(netSuite.consumerSecret)} onChange={e => setNetSuiteConfig({ ...netSuite, consumerSecret: e.target.value })} placeholder="Netsuite Consumer Secret" />
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label>Netsuite Token Key</label>
                                        <input type="text" className="form-control" defaultValue={encodeValues(netSuite.tokenKey)} onChange={e => setNetSuiteConfig({ ...netSuite, tokenKey: e.target.value })} placeholder="Netsuite Token Key" />
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label>Netsuite Token Secret</label>
                                        <input type="text" className="form-control" defaultValue={encodeValues(netSuite.tokenSecret)} onChange={e => setNetSuiteConfig({ ...netSuite, tokenSecret: e.target.value })} placeholder="Netsuite Token Secret" />
                                    </div>
                                </div>
                            </div>
                        </React.Fragment>
                    : null
                }
                <div className="row">
                    <div className="col-md-4">
                        <button disabled={disabledSubmit()} onClick={submitIntegration} className="btn btn-primary">Submit</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Integrations;
