import { ReducersTypes } from "../../constants/ReducersTypes";

export const settings = (
  state = {
    data: "settings data"
  },
  action
) => {
  switch (action.type) {
    case ReducersTypes.SETTINGS:
      return { data: action.payload.data };
    default:
      return state;
  }
};

export default { settings };
