const dashboard = (
  state = {
    payablesData: [],
    receivablesData: [],
    isFetching: true,
  },
  action,
) => {
  switch (action.type) {
    case 'SET_PAYABLE_DATA':
      return {
        ...state,
        payablesData: action.payload.data,
        isFetching: action.isFetching,
      };

    case 'SET_RECEIVABLE_DATA':
      return {
        ...state,
        receivablesData: action.payload.data,
        isFetching: action.isFetching
      };

    default:
      return state;
  }
};

export default dashboard;
